import {
  Component,
  Input, Output,
  OnChanges, OnInit, OnDestroy,
  EventEmitter,
  ElementRef,
} from '@angular/core';

import { FormControl, ValidatorFn } from '@angular/forms';

import createNumberMask from 'text-mask-addons/dist/createNumberMask';

@Component({
  selector: 'app-number-input',
  templateUrl: './number-input.component.html',
  styleUrls: ['./number-input.component.css']
})

export class NumberInputComponent implements OnInit, OnChanges, OnDestroy {

  @Input() initialValue: number = null;
  @Input() label: string = '';
  @Input() prefix: string = '';
  @Input() postfix: string = '';
  @Input() placeholder: string = '';
  @Input() fieldMods: any = {};
  @Input() thousandsSeparatorSymbol: string = '';
  @Input() integerLimit: number = 9;
  @Input() clearZero: boolean = true;
  @Input() validatorNames: Array<string> = [];

  @Output() blurred: EventEmitter<number> = new EventEmitter<number>();
  @Output() inputed: EventEmitter<any> = new EventEmitter<any>();

  public mask: any = null;
  public value: string = '';
  public inputFormControl: FormControl;
  public validatorsFn: Array<ValidatorFn> = [];
  public validators: any = {};
  public validatorsEx: any = [];

  public _name: string = 'input-int';

  private _createMask(): void {
    this.mask = createNumberMask({
      prefix: this.prefix,
      suffix: this.postfix,
      thousandsSeparatorSymbol: this.thousandsSeparatorSymbol,
      integerLimit: this.integerLimit,
    });
  }

  private _initValidators(): void {
    this.validators = {
      year: this.yearValidator,
      plz: this.plzValidator,
    };

    this.validatorsFn = this.validatorNames.map((validatorName) => {
      if (validatorName in this.validators) return this.validators[validatorName];
    });
  }

  private _initFormControls(): void {
    this.inputFormControl = new FormControl(
      '', this.validatorsFn
    );
  }

  private _deserialize(value: string): string {
    const strarr = value.split('');
    value = strarr.reduce((res, letter) => {
      if (48 <= letter.charCodeAt(0) && letter.charCodeAt(0) <= 57) res += letter; // Collecting all numbers
      return res;
    }, '');
    if (this.clearZero) {value = (this.value === '0') ? '' : value; }
    return value;
  }

  constructor() {
    this.yearValidator = this.yearValidator.bind(this);
    this.plzValidator = this.plzValidator.bind(this);
  }

  public ngOnInit(): void {
    this._createMask();
    this._initValidators();
    this._initFormControls();
    this.value = String(this.initialValue);
    if (this.clearZero) {this.value = (this.value === '0') ? '' : this.value; }
  }

  public ngOnChanges(): void {
    this.value = String(this.initialValue);
    if (this.clearZero) {this.value = (this.value === '0') ? '' : this.value; }
  }

  public ngOnDestroy(): void {
  }

  public onInput(event: any, invalid: boolean) {
    this.inputed.emit(invalid);
  }

  public onBlur(event: any, invalid: boolean): void {
    if (invalid) return;
    this.value = this._deserialize(this.value);
    this.blurred.emit(Number(this.value));
  }

  public get isFilled(): boolean {
    return this.value !== '';
  }

  yearValidator(control: FormControl): {[s: string]: boolean} {
    if (((+control.value < 1900) || (+control.value > (new Date()).getFullYear())) && control.value !== '') {
      return {'yearErr': true};
    }
    return null;
  }

  plzValidator(control: FormControl): {[s: string]: boolean} {
    if (control.value.length > 6) {
      return {'plzErr': true};
    }
    return null;
  }
}
