import {
  Component,
  Inject,
  Input,
  OnDestroy,
  OnInit,
} from '@angular/core';
import { Router } from '@angular/router';

import { debounce } from 'lodash';
import { Subscription } from 'rxjs';

import { SQUARE_COORDINATES_COEFFICIENT } from '../../../app.constants';
import { SearchPointData } from '../../../core/models/search.model';
import { MapObject } from '../../../core/models/location.model';
import { MapSettings } from '../../../core/models/map-settings.model';
import { MapsAutocompleteService } from '../../../core/services/maps-autocomplete.service';
import { MapManagerService } from '../../../core/services/map-manager.service';
import { PropertiesRepositoryService } from '../../../core/services/properties-repository.service';
import { WINDOW } from '../../../core/services/window.provider';

const CITY_NUREMBERG = 'Nuremberg';

@Component({
  selector: 'app-find-property',
  templateUrl: './find-property.component.html',
  styleUrls: [
    './find-property.component.css',
  ],
})
export class FindPropertyComponent implements OnInit, OnDestroy {
  public name: string = 'find-property';

  @Input()
  findPropertyButtonLabel: string;

  public searchPropertyId: string = null;
  public searchPointData: SearchPointData = null;

  private subscriptions: Subscription = new Subscription();

  constructor(
    @Inject(WINDOW) private window: Window,
    private router: Router,
    private mapManager: MapManagerService,
    private propertiesRepository: PropertiesRepositoryService,
    private mapsAutocompleteService: MapsAutocompleteService,
  ) {
    this.goToMarkerView = debounce(this.goToMarkerView.bind(this), 1000);
    this.initAutocompleteService = debounce(this.initAutocompleteService.bind(this), 1000);
  }

  public ngOnInit(): void {
    this.initAutocompleteService();
    this.getSearch();
  }

  public ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  public search(): void {
    this.mapsAutocompleteService.submitSearch(CITY_NUREMBERG);
    this.goToMarkerView();
  }

  public goToMarkerView(): void {
    if (this.searchPropertyId) {
      this.window.open(`/property-detail/${this.searchPropertyId}`, '_blank');
      this.searchPropertyId = null;
      this.searchPointData = null;
    } else {
      this.router.navigateByUrl('/map');
    }
  }

  private initAutocompleteService(): void {
    this.mapsAutocompleteService.setInstance(google);
  }

  private async fetchMarkers(): Promise<void> {
    this.searchPropertyId = await this.propertiesRepository.getMarkerIdFromFragmentsList({
      coordinates: {
        nwLatitude: this.searchPointData.placePoint.lat + SQUARE_COORDINATES_COEFFICIENT,
        nwLongitude: this.searchPointData.placePoint.lng + SQUARE_COORDINATES_COEFFICIENT,
        seLatitude: this.searchPointData.placePoint.lat - SQUARE_COORDINATES_COEFFICIENT,
        seLongitude: this.searchPointData.placePoint.lng - SQUARE_COORDINATES_COEFFICIENT,
      },
      zoom: 19,
    }, this.searchPointData);
  }

  private getSearch(): void {
    this.getSearchPlace();
    this.getSearchBuilding();
    this.getSearchProperty();
  }

  private getSearchProperty(): void {
    const subscription = this.mapManager.searchProperty$
      .subscribe(async (placeData: SearchPointData) => {
        this.searchPointData = placeData;
        await this.fetchMarkers();
      });
    this.subscriptions.add(subscription);
  }

  private getSearchPlace(): void {
    const subscription = this.mapManager.placeChanger$
      .subscribe((mapObject: MapObject) => {
        this.mapManager.placeChangerData = mapObject;
      });
    this.subscriptions.add(subscription);
  }

  private getSearchBuilding(): void {
    const subscription = this.mapManager.settingsUpdate$
      .subscribe((mapSettings: MapSettings) => {
        this.mapManager.mapSettingsData = mapSettings;
      });
    this.subscriptions.add(subscription);
  }
}
