import { Injectable, OnDestroy, Inject } from '@angular/core';

import { BehaviorSubject, Observable, Subject, Subscription } from 'rxjs';
import { cloneDeep } from 'lodash';

import { FilterOptions } from '../models/filter-options.model';
import { CoordinatesOptions } from '../models/coordinates.model';
import { FILTER_INITIAL_VALUES } from '../../app.constants';

import { StateManagerService } from './state-manager.service';
import { LOCAL_STORAGE } from './local-storage.provider';

@Injectable()
export class MapFilterService implements OnDestroy {

  private filterOptions$: BehaviorSubject<Object> = new BehaviorSubject<Object>({});
  private coordinatesOptionsSubject: BehaviorSubject<CoordinatesOptions> = new BehaviorSubject<CoordinatesOptions>({
                                                                       coordinates: null,
                                                                       zoom: null
                                                                     });
  private authSubscription: Subscription;

  public filtersShown = true;
  public temporaryFilters: FilterOptions = {} as FilterOptions;
  public filters: FilterOptions = {} as FilterOptions;
  public zoomOutForResultsSubject: Subject<boolean> = new Subject<boolean>();
  public readonly zoomOutForResults$: Observable<boolean> = this.zoomOutForResultsSubject.asObservable();
  public readonly coordinates$: Observable<CoordinatesOptions> = this.coordinatesOptionsSubject.asObservable();

  constructor(
    public stateManagerService: StateManagerService,
    @Inject(LOCAL_STORAGE) private localStorage: Storage
  ) {
    this.stateManagerService.setMapFilterService(this);
    this.clearFilters();
  }

  public confirmFilters(): void {
    if (JSON.stringify(this.filters) === JSON.stringify(this.temporaryFilters)) return;
    this.filters = cloneDeep(this.temporaryFilters);
    this.filterOptions$.next(this.filters);
  }

  public clearFilters(): void {
    const filterData = this.localStorage.getItem('filterData');

    if (filterData) {
      this.temporaryFilters = JSON.parse(filterData);
      this.localStorage.removeItem('filterData');
    } else {
      this.temporaryFilters = cloneDeep(FILTER_INITIAL_VALUES);
    }

    this.confirmFilters();
  }

  public toggleFiltersShown(value: boolean): void {
    this.filtersShown = value;
  }

  public getFilterStream(): BehaviorSubject<Object> {
    return this.filterOptions$;
  }

  public coordinatesStreamNext(options: CoordinatesOptions): void {
    this.coordinatesOptionsSubject.next(options);
  }

  public setZoomOutForResults(zoomOut: boolean): void {
    this.zoomOutForResultsSubject.next(zoomOut);
  }

  public ngOnDestroy(): void {
    this.authSubscription.unsubscribe();
  }
}
