import { capitalize } from 'lodash';

import {
  MARKER_STYLE,
  MARKER_POLYGON_STYLE,
} from '../../main/main.constants';

import { MapsEventListener, MarkerLabel } from '@agm/core/services/google-maps-types';
import { Point } from './location.model';
import {} from 'googlemaps';

import MarkerIconCanvasBuilder from '../../shared/utils/marker-icon-canvas-builder.util';
import { toNumber } from '../../shared/utils/to-number.util';
import { statusToBackEnd, statusToFrontEnd, propTypeToBackEnd, propTypeToFrontEnd } from '../../shared/utils/deserializator.util';

import { IStyle } from './style.model';

export interface IHistogram {
  status: 'init'|'loading'|'loaded';
  data: any;
}

export interface IMarker {
  id: string;
  position: Point;
  label: MarkerLabel;
  isRendered: boolean;
  render: (mapModule: any, map: any) => void;
  hide: () => void;
  show: () => void;
  subscribe: (eventName: string, callback: (data?: any) => void) => void;
}

export type PropertyType = 'EFH' | 'MFH' | 'ETW';

export class MarkerModel implements IMarker {
  public _icon: google.maps.Icon = null;
  public _label: MarkerLabel = null;
  public _style: IStyle = null;
  public _mapMarker: google.maps.Marker = null;
  public _listeners: Map<string, MapsEventListener> = new Map<string, MapsEventListener>();

  public building: MarkerModel = null;
  public ApartmentsPropertyIds: Array<string>;
  public LivingUnits: number = 0;
  public Price: number = 0;
  public PriceM2: number = 0;
  public PriceMin: number = 0;
  public PriceMax: number = 0;
  public SalePrice: number = 0;
  public SalePriceM2: number = 0;
  public Latitude: number = 0;
  public Longitude: number = 0;
  public PropertyId: string = '';
  public PropertyType: PropertyType = null;
  public PropertySubType: string = '';
  public SellingStatus: string = '';
  public Street: string = '';
  public HouseNumber: string = '';
  public isActive: boolean = false;
  public isHovered: boolean = false;
  public _isHidden: boolean = true;

  public get id(): string {
    if (this.PropertyId && this.PropertyId !== `${void 0}`) return this.PropertyId;
    return '';
  }

  public get type(): string {
    if (this.PropertyType && this.PropertyType !== `${void 0}`) return this.PropertyType;
    return '';
  }

  public get position(): Point {
    return {
      lat: this.Latitude,
      lng: this.Longitude
    };
  }

  public get positionAsLatLng(): google.maps.LatLng {
    return {
      lat: () => this.Latitude,
      lng: () => this.Longitude
    } as google.maps.LatLng;
  }

  public get price(): number {
    return this.Price || 0;
  }

  public get priceMin(): number {
    return this.PriceMin || 0;
  }

  public get priceMax(): number {
    return this.PriceMax || 0;
  }

  public get sellingStatus(): string {
    if (['Idle', 'MakeMeMove', 'ForSale', 'Active', 'NotActiveYet'].includes(this.SellingStatus)) {
      return this.SellingStatus;
    } else {
      return 'active';
    }
  }

  public get label(): MarkerLabel {
    if (this._label == null) {
      this._label = {
        text: this._buildMarkerText(),
        fontSize: '10px',
        color: MARKER_STYLE[this.isActive ? 'ACTIVE_STATE' : 'NORMAL_STATE'].TEXT_COLOR,
        fontFamily: 'string',
        fontWeight: 'Nunito Sans'
      };
    }

    return this._label;
  }

  public get style(): IStyle {
    if (this.isActive || this.isHovered) {
      return this._style = {
        fillColor:      MARKER_POLYGON_STYLE.ACTIVE_STATE.BACKGROUND_COLOR[this.sellingStatus],
        fillOpacity:    MARKER_POLYGON_STYLE.ACTIVE_STATE.BACKGROUND_OPACTITY,
        strokeColor:    MARKER_POLYGON_STYLE.ACTIVE_STATE.STROKE_COLOR,
        strokeOpacity:  MARKER_POLYGON_STYLE.ACTIVE_STATE.STROKE_OPACITY,
        strokeWeight:   MARKER_POLYGON_STYLE.ACTIVE_STATE.STROKE_WEIGHT,
      };
    } else if (this.isHovered) {
      return this._style = {
        fillColor:      MARKER_POLYGON_STYLE.HOVERED_STATE.BACKGROUND_COLOR[this.sellingStatus],
        fillOpacity:    MARKER_POLYGON_STYLE.HOVERED_STATE.BACKGROUND_OPACTITY,
        strokeColor:    MARKER_POLYGON_STYLE.HOVERED_STATE.STROKE_COLOR,
        strokeOpacity:  MARKER_POLYGON_STYLE.HOVERED_STATE.STROKE_OPACITY,
        strokeWeight:   MARKER_POLYGON_STYLE.HOVERED_STATE.STROKE_WEIGHT,
      };
    } else {
      return this._style = {
        fillColor:      MARKER_POLYGON_STYLE.NORMAL_STATE.BACKGROUND_COLOR,
        fillOpacity:    MARKER_POLYGON_STYLE.NORMAL_STATE.BACKGROUND_OPACTITY,
        strokeColor:    MARKER_POLYGON_STYLE.NORMAL_STATE.STROKE_COLOR,
        strokeOpacity:  MARKER_POLYGON_STYLE.NORMAL_STATE.STROKE_OPACITY,
        strokeWeight:   MARKER_POLYGON_STYLE.NORMAL_STATE.STROKE_WEIGHT,
      };
    }
  }

  public get isRendered(): boolean {
    return this._mapMarker != null;
  }

  public get isPropertyOfTypeEFH(): boolean {
    return this.PropertyType === 'EFH';
  }

  public get isPropertyOfTypeMFH(): boolean {
    return this.PropertyType === 'MFH';
  }

  public get isPropertyOfTypeETW(): boolean {
    return this.PropertyType === 'ETW';
  }

  public get buildingType(): string {
    return this.isPropertyOfTypeEFH ? 'Haus' : 'Wohnung';
  }

  private _getIcon(): google.maps.Icon {
    const imageLogo = document.createElement('img');
    imageLogo.src = this._imageLogoUrl(this.sellingStatus);

    const {url, width, height, multiplier} = MarkerIconCanvasBuilder.build(
      this._buildMarkerText(),
      this.sellingStatus,
      this.isActive,
      imageLogo
    );
    this._icon = {
      url,
      scaledSize: new google.maps.Size(width / multiplier, height / multiplier),
      anchor:     new google.maps.Point(width / (multiplier * 2), height / (multiplier * 2)),
      size:       new google.maps.Size(width / multiplier, height / multiplier)
    };

    return this._icon;
  }

  private _buildMarkerText(): string {
    if (this.isStatusIdle) {
      return 'Inactive';
    }

    let price: string = this.isPropertyOfTypeMFH ? 'ab ' : '';
    if (this.isStatusForSale && this.SalePrice) {
      price += `${this.SalePrice.toLocaleString('de-DE')} €`;
    } else {
      price += `${this.Price.toLocaleString('de-DE')} €`;
    }
    return price;
  }

  private async _updateIcon(): Promise<void> {
    this._icon = null;
    if (this._mapMarker == null) return;
    this._mapMarker.setIcon(await this._getIcon());
  }

  private _imageLogoUrl(status): string {
    switch (status) {
      case 'Idle' : return '../../assets/images/grey.svg';
      case 'MakeMeMove' : return '../../assets/images/blueplus.svg';
      case 'ForSale' : return '../../assets/images/purple.svg';
      case 'Active' : return '../../assets/images/blue.svg';
      default: return '../../assets/images/blue.svg';
    }
  }

  constructor(data: any) {
    this.deserialize(data);
  }

  public serialize(): any {
    const data: any = {};

    data.apartments = this.ApartmentsPropertyIds || [];
    data.numberOfFlats = this.LivingUnits;
    data.estimatedPrice = this.Price;
    data.estimatedPriceM2 = this.PriceM2;
    data.salePrice = this.SalePrice;
    data.salePriceM2 = this.SalePriceM2;
    data.coordinates = [this.Longitude, this.Latitude];
    data.uuid = this.PropertyId ;
    data.propertyType = propTypeToBackEnd(this.PropertyType);
    data.propertySubType = this.PropertySubType;
    data.sellingStatus = statusToBackEnd(this.SellingStatus);
    data.streetName = this.Street;
    data.streetNumber = this.HouseNumber;

    return data;
  }

  public deserialize(data: any): void {
    this.ApartmentsPropertyIds = data.apartments || [];
    this.LivingUnits = toNumber(data.numberOfFlats, 1);
    this.Price = toNumber(data.estimatedPrice, 0);
    this.PriceM2 = toNumber(data.estimatedPriceM2, 0);
    this.SalePrice = toNumber(data.salePrice, null);
    this.SalePriceM2 = toNumber(data.salePriceM2, null);
    this.PriceMin = toNumber(data.PriceMin, this.Price);
    this.PriceMax = toNumber(data.PriceMax, this.Price);
    this.Longitude = (data.coordinates && data.coordinates.coordinates) ? data.coordinates.coordinates[0] : 0;
    this.Latitude = (data.coordinates && data.coordinates.coordinates) ? data.coordinates.coordinates[1] : 0;
    this.PropertyId = data.uuid;
    this.PropertyType = propTypeToFrontEnd(data.propertyType);
    this.PropertySubType = data.propertySubType;
    this.SellingStatus = statusToFrontEnd(data);
    this.Street = data.streetName;
    this.HouseNumber = data.streetNumber;
  }

  public activate(): void {
    this.isActive = true;
    this._updateIcon();
  }

  public deactivate(): void {
    this.isActive = false;
    this._updateIcon();
  }

  public render(mapsModule: any, map: google.maps.Map, defaultIcon?: boolean): void {
    if (defaultIcon) this.renderMarkerDefault(mapsModule, map);
    else this.renderMarker(mapsModule, map);

    this._isHidden = false;

    this.enableFeature('hover');
  }

  public renderIfNeed(mapsModule: any, map: google.maps.Map): void {
    if (this._mapMarker == null) this.renderMarker(mapsModule, map);

    this.enableFeature('hover');
  }

  public renderMarker(mapsModule: any, map: google.maps.Map): void {
    this._mapMarker = new mapsModule.Marker({
      map: map,
      position: this.position,
      icon: this._getIcon(),
      optimized: true,
    });
    this._isHidden = false;
  }

  public async renderMarkerDefault(mapsModule: any, map: any): Promise<void> {
    this._mapMarker = new mapsModule.Marker({
      map: map,
      position: this.position,
      optimized: false,
    });
    this._isHidden = false;
  }

  public hide(): void {
    if (this._mapMarker != null) {
      this._mapMarker.setMap(null);
      this._mapMarker.setVisible(false);
      this._isHidden = true;
    }

    this.deactivate();
  }

  public show(): void {
    if (!this._isHidden) return;
    if (this._mapMarker != null) {
      this._mapMarker.setVisible(true);
      this._isHidden = false;
    }
  }

  public async subscribe(eventName: string, callback: (...args) => void, uniqKey: string = `${Date.now()}${Math.random()}`): Promise<{}> {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        const listenerName = `${eventName}|${uniqKey}`;
        if (this._mapMarker != null && !this._listeners.has(`${'_mapMarker'}|${listenerName}`)) {
          this._listeners.set(
            `${'_mapMarker'}|${listenerName}`,
            this._mapMarker.addListener(eventName, (...args) => callback(...args, 'marker'))
          );
        }
        resolve();
      }, 4);
    });
  }

  public unsubscribe(map: any): void {
    if (this._mapMarker != null) map.event.clearInstanceListeners(this._mapMarker);
  }

  public enableFeature(featureName: string): void {
    const activatorName = `_enable${capitalize(String(featureName))}Feature`;
    if (this[activatorName] instanceof Function) this[activatorName]();
  }

  public clearMap(): void {
    if (this._mapMarker != null) this._mapMarker.setMap(null);
  }

  public get isStatusForSale(): boolean {
    return this.SellingStatus === 'ForSale';
  }

  public get isStatusMakeMeMove(): boolean {
    return this.SellingStatus === 'MakeMeMove';
  }

  public get isStatusIdle(): boolean {
    return this.SellingStatus === 'Idle';
  }

  public get isStatusActive(): boolean {
    return this.SellingStatus === 'Active';
  }

  public get isStatusNotActiveYet(): boolean {
    return this.SellingStatus === 'NotActiveYet';
  }

  public isEqual(marker: MarkerModel): boolean {
    return JSON.stringify(marker) === JSON.stringify(this);
  }
}
