import { MapsEventListener, MarkerLabel } from '@agm/core/services/google-maps-types';

import {} from 'googlemaps';
import { IMarker, MarkerModel } from './marker.model';
import { Point } from './location.model';

import MarkerClusterIconCanvasBuilder from '../../shared/utils/marker-cluster-icon-canvas-builder.util';

import { IStyle } from './style.model';
import { MarkerClusterSatelliteModel } from './marker-cluster-satellite.model';
import { FragmentModel } from './fragment.model';

import {
  CLUSTER_POLYGON_STYLE,
} from '../../main/main.constants';

type IconState = 'initial' | 'hovered';

export interface IMarkerCluster {
  Name: string;
  Count: number;
  Radius: number;
  Latitude: number;
  Longitude: number;
}

export class MarkerClusterModel implements IMarker, IMarkerCluster {
  private iconInitial: google.maps.Icon;
  private iconHovered:  google.maps.Icon;
  private _label: MarkerLabel;
  private _style: IStyle = null;
  private _isHovered: boolean = false;
  private listeners: Map<string, MapsEventListener> = new Map<string, MapsEventListener>();

  public Name: string = '';
  public Count: number = 0;
  public Radius: number = 0;
  public Latitude: number = 0;
  public Longitude: number = 0;

  public mapMarker: google.maps.Marker = null;
  public isActive: boolean = false;

  public markers: Map<string, MarkerModel | FragmentModel> = new Map<string, MarkerModel | FragmentModel>();
  public markerSatellite: MarkerClusterSatelliteModel = null;

  public isClientSideCalculatedCluster: boolean = false;

  public get isHovered(): boolean {
    return this._isHovered;
  }

  public set isHovered(state: boolean) {
    if (this.markerSatellite && state) this.markerSatellite.show();
    else if (this.markerSatellite && !state) this.markerSatellite.hide();

    this._isHovered = state;
    this.updateIcon();
  }

  private async getIcon(state: IconState): Promise<google.maps.Icon> {
    let _icon = null;
    const {url, width, height} = await MarkerClusterIconCanvasBuilder.build(
      this.Name,
      state,
      this.transformCountToText(this.Count),
      !this.isClientSideCalculatedCluster
    );

    _icon = {
      url,
      scaledSize: new google.maps.Size(width / 2, height / 2),
      anchor:     new google.maps.Point(width / 4, height / 4),
      size:       new google.maps.Size(width, height)
    };

    return _icon;
  }

  private async updateIcon(): Promise<void> {
    if (this.mapMarker != null) this.mapMarker.setIcon(this.isHovered ? this.iconHovered : this.iconInitial);
  }

  private transformCountToText(count: number = 0): {count: string, label: string} {
    const res = {
      count: '',
      label: '',
    };

    if (count > 999999) {
      res.count = `${Math.round(count / (10 ** 5)) / 10}`;
      res.label = 'Mio. Immobilien';
    } else if (count > 1) {
      res.count = `${count.toLocaleString('de-DE')}`;
      res.label = 'Immobilien';
    } else {
      res.count = `${count.toLocaleString('de-DE')}`;
      res.label = 'Immobilie';
    }

    return res;
  }

  private computeDistanceBetween(fromPoint: google.maps.LatLng, toPoint: google.maps.LatLng): number {
    try {
      return google.maps.geometry.spherical.computeDistanceBetween(fromPoint, toPoint);
    } catch (error) {
      return 0;
    }
  }

  private containMarker(marker: MarkerModel | FragmentModel): boolean {
    const distance = this.computeDistanceBetween(
      new google.maps.LatLng(this.position.lat, this.position.lng),
      new google.maps.LatLng(marker.position.lat, marker.position.lng)
    );
    return distance < this.radius * 2.5;
  }

  private renderSatellite(mapsModule: any, map: any): void {
    this.markerSatellite.renderMarker(mapsModule, map);
  }

  constructor(data: any) {
    this.deserialize(data);
  }

  public get id(): string {
    return this.Name || `${this.Latitude + this.Longitude}`;
  }

  public get position(): Point {
    return {
      lat: this.Latitude,
      lng: this.Longitude
    };
  }

  public set position(value: Point) {
    this.Latitude = value.lat;
    this.Longitude = value.lng;
    if (this.mapMarker != null) this.mapMarker.setPosition(value);
  }

  public get label(): MarkerLabel {
    if (this._label == null) {
      this._label = {
        text: `${(this.Count)}`,
        fontSize: '12px',
        fontFamily: 'Nunito Sans',
        color: 'hsl(185, 71%, 47%)',
        fontWeight: 'normal'
      };
    }

    return this._label;
  }

  public get style(): IStyle {
    if (this.isActive || this.isHovered) {
      return this._style = {
        fillColor:      CLUSTER_POLYGON_STYLE.ACTIVE_STATE.BACKGROUND_COLOR,
        fillOpacity:    CLUSTER_POLYGON_STYLE.ACTIVE_STATE.BACKGROUND_OPACTITY,
        strokeColor:    CLUSTER_POLYGON_STYLE.ACTIVE_STATE.STROKE_COLOR,
        strokeOpacity:  CLUSTER_POLYGON_STYLE.ACTIVE_STATE.STROKE_OPACITY,
        strokeWeight:   CLUSTER_POLYGON_STYLE.ACTIVE_STATE.STROKE_WEIGHT,
      };
    } else if (this.isHovered) {
      return this._style = {
        fillColor:      CLUSTER_POLYGON_STYLE.HOVERED_STATE.BACKGROUND_COLOR,
        fillOpacity:    CLUSTER_POLYGON_STYLE.HOVERED_STATE.BACKGROUND_OPACTITY,
        strokeColor:    CLUSTER_POLYGON_STYLE.HOVERED_STATE.STROKE_COLOR,
        strokeOpacity:  CLUSTER_POLYGON_STYLE.HOVERED_STATE.STROKE_OPACITY,
        strokeWeight:   CLUSTER_POLYGON_STYLE.HOVERED_STATE.STROKE_WEIGHT,
      };
    } else {
      return this._style = {
        fillColor:      CLUSTER_POLYGON_STYLE.NORMAL_STATE.BACKGROUND_COLOR,
        fillOpacity:    CLUSTER_POLYGON_STYLE.NORMAL_STATE.BACKGROUND_OPACTITY,
        strokeColor:    CLUSTER_POLYGON_STYLE.NORMAL_STATE.STROKE_COLOR,
        strokeOpacity:  CLUSTER_POLYGON_STYLE.NORMAL_STATE.STROKE_OPACITY,
        strokeWeight:   CLUSTER_POLYGON_STYLE.NORMAL_STATE.STROKE_WEIGHT,
      };
    }
  }

  public get isRendered(): boolean {
    return this.mapMarker != null;
  }

  public get count(): number {
    return this.Count;
  }

  public set count(value: number) {
    this.Count = value;
  }

  public get radius(): number {
    return this.Radius;
  }

  public set radius(value: number) {
    this.Radius = value;
  }

  public deserialize(data: any): void {
    const properties = data.properties;

    this.Name    = properties.name;
    this.Count   = properties.count;
    this.Radius  = properties.radius || 500;
    this.Latitude  = properties.coordinates ? Number(properties.coordinates.coordinates[1]) : 0;
    this.Longitude = properties.coordinates ? Number(properties.coordinates.coordinates[0]) : 0;
    this.isClientSideCalculatedCluster = !!properties.isClientSideCalculatedCluster;

    this.markerSatellite = new MarkerClusterSatelliteModel(data);
  }

  public render(mapsModule: any, map: any): void {
    this.renderMarker(mapsModule, map);
  }

  public renderIfNeed(mapsModule: any, map: any): void {
    if (this.mapMarker == null) this.renderMarker(mapsModule, map);
  }

  public renderMarker(mapsModule: any, map: any): void {
    (async () => {
      this.iconInitial = await this.getIcon('initial');
      this.mapMarker = new mapsModule.Marker({
        map: map,
        position: this.position,
        icon: this.iconInitial,
        optimized: true,
      });
    })();

    (async () => {
      this.iconHovered = await this.getIcon('hovered');
    })();

    this.renderSatellite(mapsModule, map);
  }

  public hide(): void {
    if (this.mapMarker != null) this.mapMarker.setVisible(false);
  }

  public show(): void {
    if (this.mapMarker != null) this.mapMarker.setVisible(true);
  }

  public subscribe(eventName: string, callback: (e) => void, uniqKeyParameter?: string): void {
    const uniqKey = uniqKeyParameter || `${Date.now()}${Math.random()}`;
    const listenerName = `${eventName}|${uniqKey}`;
    if (this.mapMarker == null) {
      return void setTimeout(this.subscribe.bind(this, eventName, callback, uniqKeyParameter), 100);
    }
    if (this.mapMarker != null && !this.listeners.has(`${'mapMarker'}|${listenerName}`)) {
      this.listeners.set(`${'mapMarker'}|${listenerName}`, this.mapMarker.addListener(eventName, callback));
    }
  }

  public unsubscribe(map: any): void {
    if (this.markerSatellite) this.markerSatellite.unsubscribe(map);
    if (this.mapMarker) map.event.clearInstanceListeners(this.mapMarker);
  }

  public delete(): void {
    if (this.markerSatellite) this.markerSatellite.delete();

    if (this.mapMarker) {
      this.mapMarker.setMap(null);
      this.mapMarker = null;
      this.hide();
    }
  }

  public appendMarker(marker: MarkerModel | FragmentModel): boolean {
    if (this.containMarker(marker)) {
      if (!this.markers.has(marker.id)) {
        this.markers.set(marker.id, marker);
      }
      return true;
    } else {
      if (this.markers.has(marker.id)) {
        this.markers.delete(marker.id);
      }
      return false;
    }
  }

  public async update(data: MarkerClusterModel): Promise<void> {
    if (data && data.count != null) {
      this.count = data.count;

      this.iconInitial = await this.getIcon('initial');

      this.mapMarker.setIcon(this.iconInitial);

      this.iconHovered = await this.getIcon('hovered');
    }
  }
}
