import { ProcessType } from './signup-info.serializer';
import {
  OfferPrice,
  PropertyTypesBE,
  SearchAlertsScheme,
  SellingStatusesBE,
} from './properties-repository.model';

export class CreateSearchAlertsSignupInfo {
  public name: string;
  public city: string;
  public priceMin: number;
  public priceMax: number;
  public livingAreaMin: number;
  public livingAreaMax: number;
  public propertyTypes: Array<PropertyTypesBE>;
  public sellingStatuses: Array<SellingStatusesBE>;

  constructor(searchAlertsData: SearchAlertsScheme) {
     this.name = searchAlertsData.name;
     this.city = searchAlertsData.city;
     this.priceMin = searchAlertsData.priceMin;
     this.priceMax = searchAlertsData.priceMax;
     this.livingAreaMin = searchAlertsData.livingAreaMin;
     this.livingAreaMax = searchAlertsData.livingAreaMax;
     this.livingAreaMax = searchAlertsData.livingAreaMax;
     this.propertyTypes = searchAlertsData.propertyTypes;
     this.sellingStatuses = searchAlertsData.sellingStatuses;
  }
}

export class CreateOfferSignupInfo {
  public property: string;
  public price: number;
  public preferredTotalArea: number;

  constructor(createOfferData: OfferPrice) {
    this.property = createOfferData.property;
    this.price = createOfferData.price;
    this.preferredTotalArea = createOfferData.preferredTotalArea;
  }
}

export class AddFavoriteSignupInfo {
  constructor(public propertyId: string) {
  }
}

export interface SignupInfoDto {
  processType: ProcessType;
  payload: AddFavoriteSignupInfoPayloadDto | CreateSearchAlertsSignupInfoPayloadDto | CreateOfferSignupInfoPayloadDto;
}

export interface AddFavoriteSignupInfoPayloadDto {
  propertyId: string;
}

export interface CreateSearchAlertsSignupInfoPayloadDto {
  name: string;
  city: string;
  priceMin: number;
  priceMax: number;
  livingAreaMin: number;
  livingAreaMax: number;
  propertyTypes: Array<PropertyTypesBE>;
  sellingStatuses: Array<SellingStatusesBE>;
}

export interface CreateOfferSignupInfoPayloadDto {
  property: string;
  price: number;
  preferredTotalArea: number;
}
