import { Inject, Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { HTTP_Settings } from '../../app.constants';
import { Role, CognitoUser } from '../models/user.model';

import { AuthService } from './auth.service';
import { LoggerService } from './logger/logger.service';
import { LOGGER } from './logger/logger.provider';


export interface DjangoUser {
  role: Role;
  uuid: string;
}

@Injectable()
export class UserService {
  private _role: Role = null;
  private _uuid: string = null;

  public owner: Array<string> = ['Idle', 'default', 'MakeMeMove', 'ForSale'];
  public realtor: Array<string> = ['default', 'ForSale'];

  private updateUser(): void {
    if (this.user && this._role == null) this.getUserRole();
  }

  constructor (
    private http: HttpClient,
    private authService: AuthService,
    @Inject(LOGGER) private logger: LoggerService,
  ) {
    setTimeout(this.updateUser.bind(this), 0);
  }

  public get user(): CognitoUser {
    return this.authService.getCurrentUser();
  }

  public get uuid(): string {
    return this._uuid;
  }

  public get role(): Role {
    return this.user != null ? this._role : null;
  }

  public get isOwnerRole(): boolean {
    return this.role === 'owner';
  }

  public get isRealtorRole(): boolean {
    return this.role === 'realtor';
  }

  public async setUserRole(role: Role): Promise<void> {
    try {
      const { uuid } = await this.http.patch<DjangoUser>(`${HTTP_Settings.host}/profile`, { role }).toPromise();
      const {'custom:role': roleAttribute } = await this.authService.getUserAttributes();

      this._uuid = uuid;
      this._role = roleAttribute;
    } catch (error) {
      this.logger.error('UserService::setUserRole:Error', error);
    }
  }

  public async getUserRole(): Promise<void> {
    try {
      const {'custom:role': roleAttribute } = await this.authService.getUserAttributes();
      this._role = roleAttribute;

      const { uuid } = await this.http.get<DjangoUser>(`${HTTP_Settings.host}/profile`).toPromise();
      this._uuid = uuid;
    } catch (error) {
      this.logger.error('UserService::getUserRole:Error', error);
    }
  }

  public clearUserRole(): void {
    this._uuid = null;
    this._role = null;
  }
}
