import { Point } from './location.model';

import { MapsEventListener } from '@agm/core/services/google-maps-types';
import {} from 'googlemaps';

import DistrictTextCanvasBuilder from '../../shared/utils/district-text-builder.util';

export class MarkerClusterSatelliteModel {
  private _icon: google.maps.Icon = null;
  private _isHovered: boolean = false;
  private _listeners: Map<string, MapsEventListener> = new Map<string, MapsEventListener>();

  public Name: string;
  public Latitude: number;
  public Longitude: number;
  public mapMarker: google.maps.Marker;
  public isActive: boolean = false;

  public get isHovered(): boolean {
    return this._isHovered;
  }

  public set isHovered(state: boolean) {
    if (state) this._updateIcon();
    this._isHovered = state;
  }

  public get position(): Point {
    return {
      lat: this.Latitude + 0.002,
      lng: this.Longitude
    };
  }

  public set position(value: Point) {
    this.Latitude = value.lat;
    this.Longitude = value.lng;
    if (this.mapMarker != null) this.mapMarker.setPosition(value);
  }

  private _getIcon(): google.maps.Icon {
    if (this._icon == null) {
      const {url, width, height} = DistrictTextCanvasBuilder.build(this.Name);
      this._icon = {
        url,
        scaledSize: new google.maps.Size(width / 2, height / 2),
        anchor:     new google.maps.Point(width / 4, height / 4),
        size:       new google.maps.Size(width, height)
      };
    }

    return this._icon;
  }

  private async _updateIcon(): Promise<void> {
    if (this.mapMarker != null) {
      this._icon = null;
      this.mapMarker.setIcon(await this._getIcon());
    }
  }

  constructor(data: any) {
    this.deserialize(data);
  }


  public deserialize(data: any): void {
    const properties = data.properties;
    this.Name    = properties.name;
    this.Latitude  = properties.coordinates ? Number(properties.coordinates.coordinates[1]) : 0;
    this.Longitude = properties.coordinates ? Number(properties.coordinates.coordinates[0]) : 0;
  }


  public renderMarker(mapsModule: any, map: any): void {
    this.mapMarker = new mapsModule.Marker({
      map: map,
      position: this.position,
      icon: this._getIcon(),
      optimized: true,
    });

    this.hide();
  }

  public subscribe(eventName: string, callback: (e) => void, uniqKeyParameter?: string): void {
    const uniqKey = uniqKeyParameter || `${Date.now()}${Math.random()}`;
    const listenerName = `${eventName}|${uniqKey}`;

    if (this.mapMarker == null) {
      return void setTimeout(this.subscribe.bind(this, eventName, callback, uniqKeyParameter), 100);
    }

    if (this.mapMarker != null && !this._listeners.has(`${'mapMarker'}|${listenerName}`)) {
      this._listeners.set(`${'mapMarker'}|${listenerName}`, this.mapMarker.addListener(eventName, callback));
    }
  }

  public unsubscribe(map: any): void {
    if (this.mapMarker) map.event.clearInstanceListeners(this.mapMarker);
  }

  public delete(): void {
    if (this.mapMarker) {
      this.mapMarker.setMap(null);
      this.mapMarker = null;
      this.hide();
    }
  }

  public hide(): void {
    if (this.mapMarker != null) this.mapMarker.setVisible(false);
  }

  public show(): void {
    if (this.mapMarker != null) this.mapMarker.setVisible(true);
  }
}
