import {
  AfterViewInit,
  Component,
} from '@angular/core';
import { Router } from '@angular/router';

import { NgxModalStack } from 'ngx-multi-modal';
import { debounce } from 'lodash';

import { MapFilterService } from '../../../../core/services/map-filter.service';
import { SignupResult, SignupModalComponent } from '../../components/signup-modal/signup-modal.component';
import { AuthService } from '../../../../core/services/auth.service';

@Component({
  selector: 'app-signup-page',
  template: '',
})
export class SignupPageComponent implements AfterViewInit {
  constructor(
    private mapFilterService: MapFilterService,
    private modalService: NgxModalStack,
    private router: Router,
    private authService: AuthService,
  ) {
    this.openModal = debounce(this.openModal.bind(this), 300);
  }

  public async ngAfterViewInit(): Promise<void> {
    await this.openModal();
  }

  private async openModal(): Promise<void> {
    try {
      const modalRef = this.modalService.openFromComponent(SignupModalComponent);
      modalRef.result.then(result => {
        if (result === SignupResult.OpenLogin) {
          this.closeModal(false);
        } else {
          this.closeModal();
        }
      }, () => {
        this.closeModal();
      });
    } catch (error) {
      this.closeModal();
    }
  }

  private closeModal(clearSignupData: boolean = true): void {
    if (clearSignupData) {
      this.authService.clearSignupInfo();
    }
    this.router.navigate([{ outlets: { modal: null }}]);
  }
}
