import {
  Component,
  Inject,
  OnInit,
} from '@angular/core';

import {
  FormGroup,
  FormControl,
  Validators,
} from '@angular/forms';

import {
  HttpClient,
} from '@angular/common/http';

import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { NgxModalStack } from 'ngx-multi-modal';

import { AuthService } from '../../../core/services/auth.service';
import { LoggerService } from '../../../core/services/logger/logger.service';
import { LOGGER } from '../../../core/services/logger/logger.provider';
import { CustomerRelationshipManagementService } from '../../../core/services/customer-relationship-manager.service';
import {
  ConfirmationDialogComponent,
  ConfirmationDialogModel,
  ConfirmationDialogType
} from '../../../shared/components/confirmation-dialog/confirmation-dialog.component';

interface ContactMeModel {
  propertyId: string;
  type: string;
  title: string;
  buttonText: string;
  feedbackText: string;
  placeholderText: string;
}

@Component({
  selector: 'app-marker-detail-contact-me-window',
  templateUrl: './marker-detail-contact-me-window.component.html',
  styleUrls: ['./marker-detail-contact-me-window.component.css'],
})

export class MarkerDetailContactMeWindowComponent extends DialogComponent <ContactMeModel, string> implements OnInit {
  public propertyId: string;
  public type: string;
  public isLoggedIn: boolean = false;
  public title: string;
  public buttonText: string;
  public feedbackTitle: string;
  public feedbackText: string;
  public placeholderText: string = 'Gib hier mehr Details ein …';
  public _name: string = 'marker-detail-contact-me-window';

  public form: FormGroup = new FormGroup({
    givenName: new FormControl('', Validators.required),
    familyName: new FormControl('', Validators.required),
    email: new FormControl('', [Validators.required, Validators.email]),
    message: new FormControl('', Validators.required),
  });

  private sendAnalytics(type: string, propertyId: string, isError: boolean = false): void {
    const data = {
      action: 'Send message to owner',
      properties: {
        category: 'Contact',
        label: `${isError ? 'Failure' : 'Success'}: ${propertyId}`
      }
    };
    this.feedbackTitle = 'Wir haben deine Nachricht an den EIgentümer erhalten und leiten die Anfrage weiter.';
    this.feedbackText = 'Vielen Dank und weiterhin viel Erfolg auf Scoperty.';
    switch (this.type) {
      case 'Broker':
        data.action = 'Send message to broker';
        data.properties.category = 'Leads';
        this.feedbackTitle = 'Wir haben deine Makleranfrage erhalten und leiten die Anfrage weiter.';
        this.feedbackText = 'Vielen Dank und weiterhin viel Erfolg auf Scoperty.';
        break;
      case 'InterestedPerson':
        data.action = 'Send message to prospect';
        this.feedbackTitle = 'Vielen Dank, die Nachricht wurde versendet.';
        this.feedbackText = '';
        break;
    }
  }

  private openMessageWindow(title: string, text: string): void {
    const dialogModel: ConfirmationDialogModel = {
      message: `${title} \n\n ${text}`,
      type: ConfirmationDialogType.Info,
      buttonLabel: 'OK',
    };
    const modalRef = this.modalService.openFromComponent(ConfirmationDialogComponent);
    Object.assign(modalRef.instance, dialogModel);
  }

  public getFieldTouched(key: string): boolean {
    return this.form.controls[key].invalid && (this.form.controls[key].dirty || this.form.controls[key].touched);
  }

  public getFieldErrors(key: string): boolean {
    return this.form.controls[key].errors.required;
  }

  public getFieldInvalid(key: string): boolean {
    return this.form.controls[key].errors[key] && !this.getFieldErrors(key);
  }

  public getFieldState(key: string): string {
    return this.form.controls[key].value
      && (this.form.controls[key].dirty || this.form.controls[key].touched)
      ? 'filled' : 'empty';
  }

  constructor(
    private authService: AuthService,
    private httpClient: HttpClient,
    private customerRelationshipManagementService: CustomerRelationshipManagementService,
    public dialogService: DialogService,
    private modalService: NgxModalStack,
    @Inject(LOGGER) private logger: LoggerService,
  ) {
    super(dialogService);
  }

  public ngOnInit(): void {
    if (this.authService.isUserLoggedIn()) {
      this.isLoggedIn = true;
      this.fillForm();
    }
  }

  public async sendRequest(): Promise<void> {
    if (this.form.invalid) return;
    const ContactRequest = this.form.value;
    ContactRequest.property = this.propertyId;
    try {
      this.customerRelationshipManagementService.contactOwner(ContactRequest);
      this.sendAnalytics(this.type, this.propertyId);
    } catch (error) {
      this.logger.error('MarkerDetailContactMeWindowComponent::sendRequest', error);
      this.sendAnalytics(this.type, this.propertyId, true);
    }
    this.openMessageWindow(this.feedbackTitle, this.feedbackText);
    this.close();
  }

  public async fillForm(): Promise<void> {
    const {
      given_name: givenName,
      family_name: familyName,
      email: email
    } = await this.authService.getUserAttributes() as any;
    this.form.patchValue({ givenName, familyName, email });
  }
}
