import { isNullOrUndefined } from '../../shared/utils/null-or-undefined.util';
import { isEmptyString } from '../../shared/utils/empty-string.util';

import {
  AddFavoriteSignupInfo,
  CreateSearchAlertsSignupInfo,
  SignupInfoDto,
  AddFavoriteSignupInfoPayloadDto,
  CreateSearchAlertsSignupInfoPayloadDto,
  CreateOfferSignupInfo, CreateOfferSignupInfoPayloadDto,
} from './signup-info.model';

export enum ProcessType {
  AddFavorite = 'add_favorite',
  CreateSearchAlerts = 'create_search_alerts',
  CreateOffer = 'create_offer',
}

export class SignupInfoSerializer {
  static deserialize(cognitoDto: string): AddFavoriteSignupInfo | CreateSearchAlertsSignupInfo | CreateOfferSignupInfo {
    if (isEmptyString(cognitoDto)) {
      return;
    }

    const dto: SignupInfoDto = JSON.parse(cognitoDto);
    let model: AddFavoriteSignupInfo | CreateSearchAlertsSignupInfo | CreateOfferSignupInfo = null;
    const {processType} = dto;
    switch (processType) {
      case ProcessType.AddFavorite:
        const addFavoriteSignupInfoPayloadDto: AddFavoriteSignupInfoPayloadDto =
          <AddFavoriteSignupInfoPayloadDto>dto.payload;
        model = new AddFavoriteSignupInfo(addFavoriteSignupInfoPayloadDto.propertyId);
        break;
      case ProcessType.CreateSearchAlerts:
        const createSearchAlertsSignupInfoPayloadDto: CreateSearchAlertsSignupInfoPayloadDto =
          <CreateSearchAlertsSignupInfoPayloadDto>dto.payload;
        model = new CreateSearchAlertsSignupInfo(createSearchAlertsSignupInfoPayloadDto);
        break;
      case ProcessType.CreateOffer:
        const createOfferSignupInfoPayloadDto: CreateOfferSignupInfoPayloadDto =
          <CreateOfferSignupInfoPayloadDto>dto.payload;
        model = new CreateOfferSignupInfo(createOfferSignupInfoPayloadDto);
        break;
    }
    return model;
}

  static serialize(model: AddFavoriteSignupInfo | CreateSearchAlertsSignupInfo | CreateOfferSignupInfo): string {
    let dto: SignupInfoDto = null;
    if (isNullOrUndefined(model)) {
      return;
    }

    switch (true) {
      case model instanceof AddFavoriteSignupInfo:
        dto = {
          processType: ProcessType.AddFavorite,
          payload: model,
        };
        break;
      case model instanceof CreateSearchAlertsSignupInfo:
        dto = {
          processType: ProcessType.CreateSearchAlerts,
          payload: model,
        };
        break;
      case model instanceof CreateOfferSignupInfo:
        dto = {
          processType: ProcessType.CreateOffer,
          payload: model,
        };
        break;
    }
    return JSON.stringify(dto);
  }
}
