import { PropertyType} from '../../core/models/favorite-estate.model';

export type SellingStatusBE = 'inactive'|'active'|'make_me_move'|'for_sale';
export type PropertyTypeBE = 'apartment'|'single_family_house';
export type EmailFrequencyBE = 'immediately'|'daily' | 'weekly' | 'disable';

interface RawSellingStatus {
  owner: string;
  realtor: string;
  sellingStatus: SellingStatusBE;
}

const typeBackEnd = {
  apartment:           'ETW',
  single_family_house: 'EFH',
  multi_family_house:  'MFH',
};

const typeFrontEnd = {
  ETW: 'apartment',
  EFH: 'single_family_house',
  MFH: 'multi_family_house',
};

const typeForTemplateFromBackEnd = {
  apartment:           'Wohnung',
  single_family_house: 'Haus',
  multi_family_house:  'Mehrfamilienhaus',
};

const typeForTemplateFromFronEnd = {
  Wohnung:          'apartment',
  Haus:             'single_family_house',
  Mehrfamilienhaus: 'multi_family_house',
};

const statusBackEnd = {
  inactive:             'Idle',
  active:               'Active',
  not_active_yet:       'NotActiveYet', // BE does't have not_active_yet selling status
  make_me_move:         'MakeMeMove',
  for_sale:             'ForSale',
};

const statusFrontEnd = {
  Idle:         'inactive',
  Active:       'active',
  NotActiveYet: 'active', // BE does't have not_active_yet selling status
  MakeMeMove:   'make_me_move',
  ForSale:      'for_sale',
};

const statusForTemplateFromBackEnd = {
  inactive:     'Deaktiviert',
  active:       'Noch nicht aktiv',
  make_me_move: 'Offen für Gebote',
  for_sale:     'Zum Verkauf',
};

const statusForTemplateFromFrontEnd = {
  Idle:         'Deaktiviert',
  MakeMeMove:   'Offen für Gebote',
  ForSale:      'Zum Verkauf',
  Active:       'Aktiv',
  NotActiveYet: 'Noch nicht aktiv', // Do not delete ! Status for property-detail
};

const notifPeriodFromBackEnd = {
  immediately : 'Sofort',
  daily       : 'Täglich',
  weekly      : 'Wöchentlich',
  none        : 'Garnicht',
};

const notifPeriodToFrontEnd = {
  Sofort      : 'immediately',
  Täglich     : 'daily',
  Wöchentlich : 'weekly',
  garnicht    : 'none',
};

export function statusToBackEnd(status: string): string {
  return statusFrontEnd[status];
}

export function statusToFrontEnd({owner, realtor, sellingStatus}: RawSellingStatus): string {
  return ((!owner && !realtor) && sellingStatus === 'active')
         ? statusBackEnd['not_active_yet']
         : statusBackEnd[sellingStatus];
}

export function statusToFrontEndOriginal(sellingStatus: SellingStatusBE): string {
  return statusBackEnd[sellingStatus];
}

export function notifPeriodToFrontend(period: string): string {
  return notifPeriodFromBackEnd[period];
}

export function notifPeriodToBackend(period: string): string {
  return notifPeriodToFrontEnd[period];
}

export function templateStatusToFrontend(status: string): string {
  return statusForTemplateFromFrontEnd[status];
}

export function templateStatusToBackend(status: string): string {
  return statusForTemplateFromBackEnd[status];
}

export function propTypeToBackEnd(type: string): string {
  return typeFrontEnd[type];
}

export function propTypeToFrontEnd(type: string): PropertyType {
  return typeBackEnd[type];
}

export function templatePropertyTypeToFrontend(type: string): string {
  return typeForTemplateFromBackEnd[type];
}

export function templatePropertyTypeToBackend(type: string): string {
  return typeForTemplateFromFronEnd[type];
}
