import { Inject, Injectable } from '@angular/core';

import { Subject } from 'rxjs';

import { GOOGLE_MAPS_API_KEY } from '../../app.constants';
import { isNullOrUndefined } from '../../shared/utils/null-or-undefined.util';

import { LoggerService } from './logger/logger.service';
import { LOGGER } from './logger/logger.provider';

type TextSearchParams<T> = T;

interface TextSearch {
  query: string;
}

@Injectable()
export class MapsAutocompleteService {

  protected GOOGLE_MAPS_SECRET_KEY: string = GOOGLE_MAPS_API_KEY;
  private service: google.maps.places.PlacesService;
  private onAutocompliteLoad = new Subject<boolean>();
  public events = this.onAutocompliteLoad.asObservable();

  private submitSearch$: Subject<string> = new Subject<string>();

  private _searchByText(params: TextSearchParams<TextSearch>): Promise<google.maps.places.PlaceResult> {
    return new Promise(resolve => {
      this.service.textSearch(params, response => {
        if (response.length && response[0].place_id) return resolve(response[0]);
      });
    });
  }

  private _getFullInfo(placeId: string): Promise<google.maps.places.PlaceResult> {
    return new Promise(resolve => {
      this.service.getDetails({ placeId }, response => {
        if (response && response.address_components) return resolve(response);
      });
    });
  }

  constructor(@Inject(LOGGER) private logger: LoggerService) {}

  public setInstance(googleWrapper: any, map?: google.maps.Map): void {
    if (!isNullOrUndefined(map)) {
      this.service = new googleWrapper.maps.places.PlacesService(map);
    } else {
      this.service = new googleWrapper.maps.places.PlacesService(document.createElement('div'));
    }
    this.onAutocompliteLoad.next(true);
  }

  public async textSearch(params: TextSearchParams<TextSearch>): Promise<google.maps.places.PlaceResult> {
    try {
      const { place_id }: google.maps.places.PlaceResult = await this._searchByText(params);
      return await this._getFullInfo(place_id);
    } catch (error) {
      this.logger.error('MapsAutocompleteService:textSearch', error);
    }
  }

  public getSubmitSearchStream(): Subject<string> {
    return this.submitSearch$;
  }

  public submitSearch(city: string): void {
    this.submitSearch$.next(city);
  }
}
