import { Component } from '@angular/core';

import {
  DialogComponent,
  DialogService,
} from 'ng2-bootstrap-modal';
import { NgxModalStack } from 'ngx-multi-modal';

import {
  ForgotPasswordComponent,
  ForgotPasswordModel
} from '../../../features/forgot-password/components/forgot-password/forgot-password.component';
import { OpenModalService } from '../../../core/services/open-modal.service';

export interface LoginErrorModel {
  title?: string;
  email: string;
}

@Component({
  selector: 'app-login-error',
  templateUrl: './login-error.component.html',
  styleUrls: ['./login-error.component.css']
})
export class LoginErrorComponent
  extends DialogComponent <LoginErrorModel, string>
  implements LoginErrorModel {

  public title: string = '';
  public email: string = '';

  public _name: string = 'login-error';

  constructor(
    private openModalService: OpenModalService,
    public dialogService: DialogService,
    private modalService: NgxModalStack,
  ) {
    super(dialogService);
  }

  public apply(): void {
    this.close();
  }

  public openForgotPassword(): void {
    this.close();

    const dialogModel: ForgotPasswordModel = { email: this.email };
    const modalRef = this.modalService.openFromComponent(ForgotPasswordComponent);
    Object.assign(modalRef.instance, dialogModel);
  }
}
