import {
  Component,
  Input,
} from '@angular/core';

@Component({
  selector: 'app-badge',
  templateUrl: './badge.component.html',
  styleUrls: [
    './badge.component.css',
  ],
})
export class BadgeComponent {
  @Input()
  text: string;

  @Input()
  rotate: string;

  public _name: string = 'badge';

  public get cssRotate(): string {
    return `rotate(${ this.rotate })`;
  }
}
