import {
  Component,
  Input,
  OnChanges,
} from '@angular/core';

import { ISpoiler } from '../../../core/models/spoiler.model';

@Component({
  selector: 'app-spoiler',
  templateUrl: './spoiler.component.html',
  styleUrls: ['./spoiler.component.css']
})

export class SpoilerComponent implements OnChanges {

  @Input() item: ISpoiler;
  @Input() spoilerHeight: string = 'default';

  public _name: string = 'spoiler';
  public spoiler: any;

  private setInitialState(): void {
    if (this.item == null) {
      this.spoiler = { opened: false, title: '', content: '' };
      return;
    }
    this.spoiler = this.item;
  }

  constructor( ) {}

  public ngOnChanges(): void {
    this.setInitialState();
  }

  public toggleSpoiler(): void {
    this.spoiler.opened = !this.spoiler.opened;
  }
}
