import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';

import { DialogService, DialogComponent } from 'ng2-bootstrap-modal';
import { Subscription } from 'rxjs';

import { StateManagerService } from '../../../../core/services/state-manager.service';
import { MapFilterService } from '../../../../core/services/map-filter.service';
import { isSmallMobileDevice, isMobileDevice } from '../../../../shared/utils/responsive.util';


export interface SuccessSignupModel {
  role:   string;
  title:  string;
  text:   string;
  button: string;
  buttonInactive: string;
}

@Component({
  selector: 'app-success-signup',
  templateUrl: './success-signup.component.html',
  styleUrls: ['./success-signup.component.css']
})

export class SuccessSignupComponent
  extends DialogComponent <SuccessSignupModel, string>
  implements SuccessSignupModel, OnInit, OnDestroy {

  private _redirect: boolean = false;
  private apartmentsListChanged: Subscription = null;

  public role: string = '';
  public title: string = '';
  public text: string = '';
  public button: string = '';
  public buttonInactive: string = '';

  public isSmallMobileDevice: boolean = isSmallMobileDevice();
  public isMobileDevice: boolean = isMobileDevice();

  public _name: string = 'success-signup';

  public get isProfiRole(): boolean {
    return this.role === 'global' || this.role === 'realtor';
  }

  constructor(
    private stateManagerService: StateManagerService,
    private router: Router,
    private mapFilterService: MapFilterService,
    public dialogService: DialogService,
  ) {
    super(dialogService);
  }

  public ngOnInit(): void {
    this.apartmentsListChanged = this.stateManagerService
      .apartmentsListChanged$
      .subscribe(result => {
        if (result) this._redirect = true;
      });
  }

  public ngOnDestroy(): void {
    super.ngOnDestroy();
    if (this.apartmentsListChanged) this.apartmentsListChanged.unsubscribe();
  }

  public onLogoClick(): void {
    this.router.navigate(['/map']);
    if (this.isSmallMobileDevice) {
      setTimeout(() => {
        this.mapFilterService.toggleFiltersShown(false);
      }, 500);
    }
    this.close();
  }

  public apply(choice): void {
    if ((this.button === 'Zu deiner Immobilie' ||
        (this.button === 'Weiter suchen' &&
          (this.buttonInactive === 'Weiter zu meinem Bereich' || this.buttonInactive === 'Zu meinem Bereich')
       && choice === 'choice-property'))
       ) {
      setTimeout(() => this.router.navigate(['/profile/my-property']), 0);
    }

    if (this.button === 'Zurück zur Karte' || this.button === 'Immobilie finden' ||
       (this.button === 'Weiter suchen' &&
         (this.buttonInactive === 'Weiter zu meinem Bereich' || this.buttonInactive === 'Zu meinem Bereich')
       && choice === 'choice-map')) {
      setTimeout(() => this.router.navigate(['/map']), 0);
      if (this.isSmallMobileDevice) {
        setTimeout(() => {
          this.mapFilterService.toggleFiltersShown(false);
        }, 500);
      }
    }

    this.result = 'close';
    this.close();
  }

}
