import { capitalize } from 'lodash';
import { MapsEventListener, GoogleMap, Marker } from '@agm/core/services/google-maps-types';

import { MARKER_POLYGON_STYLE } from '../../main/main.constants';
import { statusToBackEnd, propTypeToBackEnd, propTypeToFrontEnd } from '../../shared/utils/deserializator.util';

import { IStyle } from './style.model';
import { Point } from './location.model';
import { MarkersBackEndData, InitialPropertyObject } from './properties-repository.model';

export class FragmentModel {
  public icon: google.maps.Icon = null;
  public _style: IStyle = null;
  public mapMarker: google.maps.Marker = null;
  public listeners: Map<string, MapsEventListener> = new Map<string, MapsEventListener>();
  public Latitude: number = 0;
  public Longitude: number = 0;
  public PropertyId: string = '';
  public PropertyType: string = '';
  public SellingStatus: string = '';
  public isActive: boolean = false;
  public isHovered: boolean = false;
  public isHidden: boolean = true;

  public get id(): string {
    if (this.PropertyId && this.PropertyId !== `${void 0}`) return this.PropertyId;
    return '';
  }

  public get type(): string {
    if (this.PropertyType && this.PropertyType !== `${void 0}`) return this.PropertyType;
    return '';
  }

  public get position(): Point {
    return {
      lat: this.Latitude,
      lng: this.Longitude
    };
  }

  public get positionAsLatLng(): google.maps.LatLng {
    return {
      lat: () => this.Latitude,
      lng: () => this.Longitude
    } as google.maps.LatLng;
  }

  public get sellingStatus(): string {
    if (['Idle', 'MakeMeMove', 'ForSale', 'Active', 'NotActiveYet'].includes(this.SellingStatus)) {
      return this.SellingStatus;
    } else {
      return 'active';
    }
  }

  public get style(): IStyle {
    if (this.isActive || this.isHovered) {
      return this._style = {
        fillColor:      MARKER_POLYGON_STYLE.ACTIVE_STATE.BACKGROUND_COLOR[this.sellingStatus],
        fillOpacity:    MARKER_POLYGON_STYLE.ACTIVE_STATE.BACKGROUND_OPACTITY,
        strokeColor:    MARKER_POLYGON_STYLE.ACTIVE_STATE.STROKE_COLOR,
        strokeOpacity:  MARKER_POLYGON_STYLE.ACTIVE_STATE.STROKE_OPACITY,
        strokeWeight:   MARKER_POLYGON_STYLE.ACTIVE_STATE.STROKE_WEIGHT,
      };
    } else if (this.isHovered) {
      return this._style = {
        fillColor:      MARKER_POLYGON_STYLE.HOVERED_STATE.BACKGROUND_COLOR[this.sellingStatus],
        fillOpacity:    MARKER_POLYGON_STYLE.HOVERED_STATE.BACKGROUND_OPACTITY,
        strokeColor:    MARKER_POLYGON_STYLE.HOVERED_STATE.STROKE_COLOR,
        strokeOpacity:  MARKER_POLYGON_STYLE.HOVERED_STATE.STROKE_OPACITY,
        strokeWeight:   MARKER_POLYGON_STYLE.HOVERED_STATE.STROKE_WEIGHT,
      };
    } else {
      return this._style = {
        fillColor:      MARKER_POLYGON_STYLE.NORMAL_STATE.BACKGROUND_COLOR,
        fillOpacity:    MARKER_POLYGON_STYLE.NORMAL_STATE.BACKGROUND_OPACTITY,
        strokeColor:    MARKER_POLYGON_STYLE.NORMAL_STATE.STROKE_COLOR,
        strokeOpacity:  MARKER_POLYGON_STYLE.NORMAL_STATE.STROKE_OPACITY,
        strokeWeight:   MARKER_POLYGON_STYLE.NORMAL_STATE.STROKE_WEIGHT,
      };
    }
  }

  public get isRendered(): boolean {
    return this.mapMarker != null;
  }

  public get isPropertyOfTypeEFH(): boolean {
    return this.PropertyType === 'EFH';
  }

  public get isPropertyOfTypeMFH(): boolean {
    return this.PropertyType === 'MFH';
  }

  public get isPropertyOfTypeETW(): boolean {
    return this.PropertyType === 'ETW';
  }

  public renderMarker(mapsModule: any, map: google.maps.Map): void {
    this.mapMarker = new mapsModule.Marker({
      map: map,
      position: this.position,
      icon: this.getIcon(),
      optimized: true,
    });
    this.isHidden = false;
  }

  private getIcon(): google.maps.Icon {
    let url: string = '';

    if (window['_canIUseWebP']) url = `../../assets/images/fragments/${this.SellingStatus}.webp`;
    else url = `../../assets/images/fragments/${this.SellingStatus}.png`;

    this.icon = {
      url,
      scaledSize: new google.maps.Size(12, 12),
      anchor:     new google.maps.Point(6, 6),
      size:       new google.maps.Size(12, 12)
    };

    return this.icon;
  }

  private async updateIcon(): Promise<void> {
    this.icon = null;
    if (this.mapMarker == null) return;
    this.mapMarker.setIcon(this.getIcon());
  }

  constructor(data: MarkersBackEndData) {
    this.deserialize(data);
  }

  public serialize(): InitialPropertyObject {
    const data: InitialPropertyObject = {} as InitialPropertyObject;

    data.coordinates = [this.Longitude, this.Latitude];
    data.uuid = this.PropertyId ;
    data.propertyType = propTypeToBackEnd(this.PropertyType);
    data.sellingStatus = statusToBackEnd(this.SellingStatus);

    return data;
  }

  public deserialize(data: MarkersBackEndData): void {
    this.Longitude = (data.geometry && data.geometry.coordinates) ? data.geometry.coordinates[0] : 0;
    this.Latitude = (data.geometry && data.geometry.coordinates) ? data.geometry.coordinates[1] : 0;
    this.PropertyId = data.properties.uuid;
    this.PropertyType = propTypeToFrontEnd(data.properties.propertyType);
    this.SellingStatus = data.properties.sellingStatus;
  }

  public activate(): void {
    this.isActive = true;
    this.updateIcon();
  }

  public deactivate(): void {
    this.isActive = false;
    this.updateIcon();
  }

  public render(mapsModule: any, map: google.maps.Map, defaultIcon?: boolean): void {
    if (defaultIcon) this.renderMarkerDefault(mapsModule, map);
    else this.renderMarker(mapsModule, map);

    this.isHidden = false;

    this.enableFeature('hover');
  }

  public renderIfNeed(mapsModule: any, map: google.maps.Map): void {
    if (this.mapMarker == null) this.renderMarker(mapsModule, map);

    this.enableFeature('hover');
  }

  public async renderMarkerDefault(mapsModule: any, map: google.maps.Map): Promise<void> {
    this.mapMarker = new mapsModule.Marker({
      map: map,
      position: this.position,
      optimized: true,
    } as google.maps.MarkerOptions);

    this.isHidden = false;
  }

  public hide(): void {
    if (this.mapMarker != null) {
      this.mapMarker.setMap(null);
      this.mapMarker.setVisible(false);
      this.isHidden = true;
    }

    this.deactivate();
  }

  public show(): void {
    if (!this.isHidden) return;
    if (this.mapMarker != null) {
      this.mapMarker.setVisible(true);
      this.isHidden = false;
    }
  }

  public subscribe(eventName: string, callback: (...args) => void, uniqKey: string = `${Date.now()}${Math.random()}`): void {
    const listenerName = `${eventName}|${uniqKey}`;
    if (this.mapMarker != null && !this.listeners.has(`${'mapMarker'}|${listenerName}`)) {
      this.listeners.set(
        `${'mapMarker'}|${listenerName}`,
        this.mapMarker.addListener(eventName, (...args) => callback(...args, 'marker'))
      );
    }
  }

  public unsubscribe(map): void {
    if (this.mapMarker != null) map.event.clearInstanceListeners(this.mapMarker);
  }

  public enableFeature(featureName: string): void {
    const activatorName = `_enable${capitalize(String(featureName))}Feature`;
    if (this[activatorName] instanceof Function) this[activatorName]();
  }

  public clearMap() {
    if (this.mapMarker != null) this.mapMarker.setMap(null);
  }
}
