import { statusToFrontEnd, propTypeToFrontEnd } from '../../shared/utils/deserializator.util';
import { isMobileMediumDevice } from '../../shared/utils/responsive.util';

export type PropertyType = 'EFH' | 'MFH' | 'ETW';
export interface FavoriteImage {
  uuid: string;
  image: string;
  imageThumbnail: string;
}

export interface IFavorite {
  uuid:             string;
  salePrice:        number;
  estimatedPrice:   number;
  estimatedPriceM2: number;
  offerPrice:       number;
  sellingStatus:    string;
  estateImgUrl:     string;
  propertyType:     PropertyType;
  country:          string; // Default : "DE"
  state:            string;
  county:           string;
  city:             string;
  streetName:       string;
  streetNumber:     string;
  zipCode:          string;
  roomsNumber:      number;
  floor:            number;
  livingArea:       number;
  images:           Array<FavoriteImage>;
  constructionYear: string;

  realtyProperties:         Array<string>;
  fullAddress?:             string;
  updatedSellingStatus?:    string;
  receiveNotifications?:    boolean; // @FIXME: check API
}

export class FavoriteModel implements IFavorite {
  private _isMobileMediumDevice: boolean = isMobileMediumDevice();

  public uuid:             string;
  public salePrice:        number;
  public estimatedPrice:   number;
  public estimatedPriceM2: number;
  public offerPrice:       number;
  public sellingStatus:    string;
  public estateImgUrl:     string;
  public srcImg:           string;
  public propertyType:     PropertyType;
  public country:          string;
  public state:            string;
  public county:           string;
  public city:             string;
  public streetName:       string;
  public streetNumber:     string;
  public zipCode:          string;
  public roomsNumber:      number;
  public floor:            number;
  public livingArea:       number;
  public images:           Array<FavoriteImage>;
  public constructionYear: string;
  public favorite: boolean;

  public realtyProperties:         Array<string>;
  public fullAddress?:             string;
  public updatedSellingStatus?:    string;
  public receiveNotifications?:    boolean; // @FIXME: check API

  public get isPropertyOfTypeEFH(): boolean {
    return this.propertyType === 'EFH';
  }

  public get isPropertyOfTypeMFH(): boolean {
    return this.propertyType === 'MFH';
  }

  public get isPropertyOfTypeETW(): boolean {
    return this.propertyType === 'ETW';
  }

  public get isFavourite(): boolean {
    return this.favorite;
  }

  public get hasSalePrice(): boolean {
    return this.salePrice != null;
  }

  public get imageLink(): string {
    return this.images[0] ? this.images[0].image : '';
  }

  public get buildingType(): string {
    if (this.isPropertyOfTypeEFH) return 'Haus';
    else if (this.isPropertyOfTypeMFH) return 'Mehrfamilienhaus';
    else return 'Wohnung';
  }

  private _deserialize(data: any): void {
    this.uuid             = data.uuid;
    this.salePrice        = data.salePrice;
    this.estimatedPrice   = data.estimatedPrice;
    this.estimatedPriceM2 = data.estimatedPriceM2;
    this.offerPrice       = data.offerPrice || null;
    this.sellingStatus    = statusToFrontEnd(data);
    this.propertyType     = propTypeToFrontEnd(data.propertyType);
    this.estateImgUrl     = data.estateImgUrl;
    this.country          = data.country;
    this.state            = data.state;
    this.county           = data.county;
    this.city             = data.city;
    this.streetName       = data.streetName;
    this.streetNumber     = data.streetNumber;
    this.zipCode          = data.zipCode;
    this.roomsNumber      = data.numberOfRooms;
    this.floor            = data.floor;
    this.livingArea       = data.livingArea;
    this.images           = data.images;
    this.constructionYear = data.constructionYear;
    this.favorite        = true;
  }

  public get isForSale(): boolean {
    return this.sellingStatus === 'ForSale';
  }

  public get isMakeMeMove(): boolean {
    return this.sellingStatus === 'MakeMeMove';
  }

  public get isIdle(): boolean {
    return this.sellingStatus === 'Idle';
  }

  public get isActive(): boolean {
    return this.sellingStatus === 'Active';
  }

  public get isNotActiveYet(): boolean {
    return this.sellingStatus === 'NotActiveYet';
  }

  constructor(data: any) {
    this._deserialize(data);
  }
}
