import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';

import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

import { OpenModalService } from '../../../core/services/open-modal.service';
import { StateManagerService } from '../../../core/services/state-manager.service';
import { RegistrationComponent } from '../../../features/registration/pages/registration/registration.component';

export interface RoleChooseModel {
  title?: string;
}

@Component({
  selector: 'app-role-choose',
  templateUrl: './role-choose.component.html',
  styleUrls: ['./role-choose.component.css']
})

export class RoleChooseComponent
  extends DialogComponent <RoleChooseModel, string>
  implements RoleChooseModel, OnInit, OnDestroy {

  public title: string;
  public isMobileDevice: boolean = document.body.clientWidth < 768;

  public _name: string = 'role-choose';

  private _listener(): void {
    this.isMobileDevice = document.body.clientWidth < 768;
  }

  constructor(
    public dialogService: DialogService,
    private openModalService: OpenModalService,
    private stateManagerService: StateManagerService,
    private router: Router,
  ) {
    super(dialogService);

    this._listener = this._listener.bind(this);
  }

  public ngOnInit(): void {
    window.addEventListener('resize', this._listener);
  }

  public ngOnDestroy(): void {
    super.ngOnDestroy();
    window.removeEventListener('resize', this._listener);
  }

  public onLogoClick(): void {
    if (this.stateManagerService.isMapPage) this.router.navigate(['/']);
    else this.router.navigate(['/map']);
  }

  public apply(role: string): void {
    this.openModalService.open();
    this.dialogService.addDialog(
      RegistrationComponent,
      { role: role, mode: 'registration' }
    )
      .subscribe((message) => {
        this.openModalService.close();
      });
    this.close();
  }
}
