import { Pipe, PipeTransform } from '@angular/core';

import { PropertyTypeBE, templatePropertyTypeToFrontend } from '../utils/deserializator.util';

@Pipe({
  name: 'propertyTypes',
})
export class PropertyTypesPipe implements PipeTransform {
  transform(propertyTypes: Array<PropertyTypeBE>): string {
    return propertyTypes.map(propertyType => templatePropertyTypeToFrontend(propertyType)).join(', ');
  }
}
