import {
  Component,
  OnInit,
  ElementRef,
  ChangeDetectorRef,
  ViewChild,
  ApplicationRef,
} from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';

import { OverlayService } from './core/services/overlay.service';
import { StateManagerService } from './core/services/state-manager.service';
import { AuthService } from './core/services/auth.service';
import { MapFilterService } from './core/services/map-filter.service';
import { ExtendedMarkerModel } from './core/models/extended-marker.model';
import { AnalyticsService } from './core/services/analytics/analytics.service';
import { clientHeight, isSmallDevice } from './shared/utils/responsive.util';
import { UserService } from './core/services/user.service';
import { CookiesPermission, CookiesService } from './core/services/cookies.service';

const scrollTo = (element, top, left) => {
  if (element.scrollTo != null) {
    element.scrollTo(top, left);
    return;
  } else if (element.scroll != null) {
    element.scroll(top, left);
    return;
  } else {
    element.scrollTop = top;
    element.scrollLeft = left;
  }
};

const SCROLL_TIMEOUT = 2500; // Seconds

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
})
export class AppComponent implements OnInit {
  @ViewChild('scrollContainer')
  scrollContainerElement: ElementRef;

  public _name: string = 'app';

  public isScroll: boolean = false;
  public isFirstFooter: boolean;
  public isInitialScreen: boolean = true;
  public overlayOn: boolean = false;
  public overlaySubscription: Subscription;
  public isSmallDevice: boolean = isSmallDevice();

  public get scrollContainer(): any {
    return this.scrollContainerElement.nativeElement;
  }

  public get cookiesState(): boolean {
    return this.cookiesService.cookiesFormIsShown;
  }

  public get isMapPage(): boolean {
    return this.stateManagerService.isMapPage;
  }

  public get markersDetailIsOpen(): boolean {
    return this.stateManagerService.markersDetailIsOpen;
  }

  public get isStartPage(): boolean {
    return this.stateManagerService.isStartPage;
  }

  public get isPropertyDetailPageForLoggedIn(): boolean {
    return this.stateManagerService.isPropertyDetailPage &&
           this.authService.isUserLoggedIn &&
           this.activeMarker.isOwnedBy(this.userService.uuid);
  }

  public get activeMarker(): ExtendedMarkerModel {
    return this.stateManagerService.activeMarker;
  }

  private _scrollStart(): void {
    const cookiesSettings = this.cookiesService.checkPermission();
    if (!cookiesSettings) this.cookiesService.cookiesFormIsShown = true;
  }

  constructor(
    private applicationRef: ApplicationRef,
    private router: Router,
    private actRoute: ActivatedRoute,
    private _ngEl: ElementRef,
    private changeDetectorRef: ChangeDetectorRef,
    private analyticsService: AnalyticsService,
    public authService: AuthService,
    public userService: UserService,
    public mapFilterService: MapFilterService,
    private overlayService: OverlayService,
    private cookiesService: CookiesService,
    public stateManagerService: StateManagerService,
  ) {
    this.fixNg2BootstrapModalLib();
    this._scrollStart = this._scrollStart.bind(this);
  }

  private fixNg2BootstrapModalLib(): void {
    // FIXME: Dirty fix for issues with the ng2-bootstrap-modal lib in angular >= v5,
    // see https://github.com/ankosoftware/ng2-bootstrap-modal/issues/49.
    // Remove after this library is replaced with a new one.
    Object.defineProperty(this.applicationRef, '_rootComponents', {
      get: () => this.applicationRef['components'],
    });
  }

  ngOnInit() {
    this.analyticsService.activateAnalytics();

    this._ngEl.nativeElement.style.height = `${clientHeight()}px`;
    window.addEventListener('resize', () => {
      this._ngEl.nativeElement.style.height = `${clientHeight()}px`;
    });
    this.overlaySubscription =
      this.overlayService.overlayStatus$.subscribe((status) => void (this.overlayOn = status));

    this.router.events
      .pipe(
        filter(event => event instanceof NavigationEnd),
      )
      .subscribe(event => {
        let currentRoute = this.actRoute.root;
        while (currentRoute.children[0] !== undefined) {
          currentRoute = currentRoute.children[0];
        }
        this.isFirstFooter = currentRoute.snapshot.data.isFirstFooter;
      });

    const scrollBlock = this._ngEl.nativeElement.querySelector('#scrollBlock');
    this.router.events
      .pipe(
        filter((event) => event instanceof NavigationEnd),
      )
      .subscribe(() => scrollTo(scrollBlock, 0, 0));
    this.stateManagerService.markersDetailIsOpenChanged.subscribe(() => this.changeDetectorRef.detectChanges());
    this.stateManagerService.isMapPageChanged.subscribe(() => this.changeDetectorRef.detectChanges());
    setTimeout(() => {
      this.isInitialScreen = false;
    }, 1000);

    if (this.isSmallDevice) this.mapFilterService.toggleFiltersShown(false);

    setTimeout(this._scrollStart, SCROLL_TIMEOUT);
  }

  onScroll(event) {
    this.isScroll = !!(event.target && event.target.scrollTop);
  }
}
