  public async getMarkerIdFromFragmentsList(options: QueryOptions, query: SearchPointData): Promise<string> {
    try {
      const params = this.optionsToParams(options);
      const responseFragments = await this.httpClient.get<PropertiesListResult>(
          `${this.URL_V1_CASHED}/geo${params}`, { headers: this.skipAuthHeaders }
        ).toPromise();

      let fragments: Array<MarkerModel> = [];
      if (responseFragments != null && responseFragments.features instanceof Array) {
        fragments = responseFragments.features.map(item => new MarkerModel(item.properties));
      }

      if (query === null || fragments.length === 0) {
        return '';
      } else {
        const markersPoints: Array<IMarkersPoints> = [];

        fragments.map(marker => {
          if (marker instanceof MarkerModel) {
            markersPoints.push(
              {
                id: marker.id,
                street: marker.Street,
                houseNumber: marker.HouseNumber,
                lat: marker.Latitude,
                lng: marker.Longitude
              }
            );
          }
        });

        for (let i = 0; i < markersPoints.length; i++) {
          markersPoints[i].distance = calculateDistance(
            query.placePoint.lat,
            query.placePoint.lng,
            markersPoints[i].lat,
            markersPoints[i].lng,
            'K'
          );
        }

        markersPoints.sort((a: IMarkersPoints, b: IMarkersPoints) => {
          return a.distance - b.distance;
        });

        markersPoints.splice(5);

        const pointStreet = String(query.address.street).slice(0, 1).toLowerCase();
        const pointStreetNumber = String(query.address.streetNumber);

        const searchProperty = markersPoints.find(markerPoint => {
          return (
            (String(markerPoint.street).slice(0, 1).toLowerCase() === pointStreet) &&
            (String(markerPoint.houseNumber) === pointStreetNumber)
          );
        });

        if (!isNullOrUndefined(searchProperty)) return searchProperty.id;
        else return null;
      }
    } catch (error) {
      this.logger.error('PropertiesRepositoryService::getFragmentsList:Error', error);
    }
  }

  public async getClusters(options: QueryOptions): Promise<ClustersList> {
    this.clustersIsGotten = true;
    /*
     Returns the list of all properties in a given area defined by the north-west and south-east
     coordinates and filter criteria passed as parameters.
     Api used by map to get only geo-related info about property to draw borders & show tooltips.
     Cursor-paginated by 1000, and optimized for map usage only.
    Should be used for medium, high zoom levels, for upper zoom levels use GET /clusters API
    */

    const clonedOptions: QueryOptions = cloneDeep(options);
    this.clearInactiveStatus(clonedOptions.filterOptions);
    const params = this.optionsToParams(clonedOptions);

    try {
      return await this.httpClient.get<ClustersList>(`${this.CLUSTERS_URL_CASHED}${params}`, { headers: this.skipAuthHeaders }).toPromise();
    } catch (error) {
      this.logger.error('PropertiesRepositoryService::getClusters:Error', error);
    }
  }

  public async getPriceHistogramList(options: QueryOptions): Promise<IHistogram> {
    /**
     * Returns price histogram data to be used on the frontend to build filter by price.
     * property_type string Choices are: ['apartment', 'single_family_house', 'multi_family_house']
    */
    const clonedOptions: QueryOptions = cloneDeep(options);
    this.clearInactiveStatus(clonedOptions.filterOptions);
    const params = this.optionsToParams(clonedOptions);

    // for histogram's loader: if there's an error, show the panel with default values, w\o graphic
    let result: IHistogram;
    try {
      result = await this.httpClient.get<IHistogram>(
          `${this.URL_V1_CASHED}/price-histogram${params}`, { headers: this.skipAuthHeaders }
        ).toPromise();
    } catch (error) {
      result = {status: 'loaded', data: null};
      this.logger.error('PropertiesRepositoryService:getPriceHistogramList:Error', error);
    } finally {
      return result;
    }
  }

