import {
  Component,
  Inject,
  OnInit,
  OnDestroy,
  Input,
  Output,
  EventEmitter,
  NgZone,
  ViewChild,
  ElementRef,
  Renderer2,
} from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { FormControl } from '@angular/forms';

import { MapsAPILoader } from '@agm/core';
import {} from 'googlemaps';
import { debounce } from 'lodash';
import { Observable, Subscription } from 'rxjs';

import { Point, Place, Address } from '../../../core/models/location.model';
import { SEARCHBAR_TOGGLE_TYPES } from '../../../main/main.constants';
import { MapManagerService } from '../../../core/services/map-manager.service';
import { MapsAutocompleteService } from '../../../core/services/maps-autocomplete.service';
import { isSmallMobileDevice } from '../../utils/responsive.util';
import { isNullOrUndefined } from '../../utils/null-or-undefined.util';
import { LoggerService } from '../../../core/services/logger/logger.service';
import { LOGGER } from '../../../core/services/logger/logger.provider';

type TypeOfSearch = 'withoutGoToMap';
type TypeOfSource = 'headerPage' | 'regPage' | 'startPage';

@Component({
  selector: 'app-search-bar',
  templateUrl: './search-bar.component.html',
  styleUrls: ['./search-bar.component.css'],
})

export class SearchBarComponent implements OnInit, OnDestroy {

  @Input() focused: boolean;
  @Input() placeholderValue: string = '';
  @Input() searchClicked: string;
  @Input() source: TypeOfSource;
  @Input() typeOfSearch: TypeOfSearch;
  @Input() page: string;
  @Input() bigField: boolean = false;
  @Input() address: string = '';
  @Input() label: string = 'Suche nach deiner Adresse';

  @Output() placeChanged: EventEmitter<{}> = new EventEmitter<{}>();
  @Output() bigSearch: EventEmitter<boolean> = new EventEmitter<boolean>();

  @ViewChild('search') searchElement: ElementRef;

  public searchControl: FormControl;
  public autocomplete: google.maps.places.Autocomplete;
  public mapsAutocompleteSubscription: Subscription;
  public _name: string = 'search-bar';
  public isSmallMobileDevice: boolean = isSmallMobileDevice();

  public subscriptionMap: Map<string, Subscription> = new Map<string, Subscription>();

  public scrollBlock: HTMLElement = null;
  public autocompleteInput: HTMLElement = null;

  private blurAutocompleteInput(): void {
    if (this.document.activeElement === this.autocompleteInput) this.autocompleteInput.blur();
  }

  private blurSearchWhenScroll(): void {
    if (!isNullOrUndefined(this.scrollBlock)) {
      this.scrollBlock.addEventListener('scroll', () => this.blurAutocompleteInput());
    }
  }

  private initHTMLElements(): void {
    this.scrollBlock = this.document.getElementById('scrollBlock');
    this.autocompleteInput = this.document.getElementById('autocompleteInput');
  }

  constructor(
    private apiLoader: MapsAPILoader,
    private ngZone: NgZone,
    private mapManager: MapManagerService,
    private mapsAutocompleteService: MapsAutocompleteService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private renderer: Renderer2,
    @Inject(LOGGER) private logger: LoggerService,
    @Inject(DOCUMENT) private document: Document,
  ) {
    this.initHTMLElements = debounce(this.initHTMLElements.bind(this), 1000);
    this.blurSearchWhenScroll = debounce(this.blurSearchWhenScroll.bind(this), 2000);
  }

  private isSearchInputNotEmpty(): boolean {
    return this.searchElement.nativeElement.value !== '';
  }

  public ngOnInit(): void {
    this.searchControl = new FormControl();
    this._loadAutocomplete();
    this.mapsAutocompleteSubscription =  this.mapsAutocompleteService.events.subscribe(opts => {});
    this.subscribeOn('checkProperty', this.mapManager.getCheckPropertyStream(), this.checkProperty);
    this.subscribeOn('submitSearch', this.mapsAutocompleteService.getSubmitSearchStream(), this.submitSearch);
    this.initHTMLElements();
    if (this.source === 'startPage') this.blurSearchWhenScroll();
  }

  public ngOnDestroy(): void {
    this.unsubscribeAll();

    // @TODO: Check event listeners and maybe destroy them
    this.mapsAutocompleteSubscription.unsubscribe();

    if (this.source === 'startPage' && !isNullOrUndefined(this.scrollBlock)) {
      this.scrollBlock.removeEventListener('scroll', () => this.blurAutocompleteInput());
    }
  }

  public subscribeOn(key: string, stream: Observable<any>, handler: (data: any) => void): void {
    if (this.subscriptionMap.has(key)) this.subscriptionMap.get(key).unsubscribe();
    this.subscriptionMap.set(key, stream.subscribe(handler.bind(this)));
  }

  public unsubscribeAll(): void {
    this.subscriptionMap.forEach((subscription) => subscription.unsubscribe());
  }

  public async checkProperty(validity: boolean): Promise<void> {
   if (!validity) {
     this.searchControl.markAsTouched();
     this.searchControl.setErrors({property: {valid: false}});
   } else {
     this.searchControl.setErrors(null);
   }
  }

  public async submitSearch(city: string): Promise<void> {
    google.maps.event.trigger(this.autocompleteInput, 'focus', {});
    google.maps.event.trigger(this.autocompleteInput, 'keydown', { keyCode: 13 });
    google.maps.event.trigger(this, 'focus', {});
    await this.textSearch(`${this.address} ${city}`);
  }

  /* add 'filled' or 'empty state to input wrapper */
  public onBlurHandler(event: FocusEvent): void {
    if (this.source === 'headerPage') {
      let parentClass: string = '';
      let toRemoveClass: string = '';
      if (this.isSearchInputNotEmpty()) {
        parentClass = 'search-bar__wrapper--state-filled';
        toRemoveClass = 'search-bar__wrapper--state-empty';
      } else {
        parentClass = 'search-bar__wrapper--state-empty';
        toRemoveClass = 'search-bar__wrapper--state-filled';
      }
      this.renderer.removeClass(this.searchElement.nativeElement.parentNode, toRemoveClass);
      this.renderer.addClass(this.searchElement.nativeElement.parentNode, parentClass);
    }
  }

  public searchHandler(state: boolean): void {
    if (this.source === 'headerPage')  this.bigSearch.emit(state);
  }

  public async _loadAutocomplete(): Promise<void> {
    try {
      await this.apiLoader.load();
      this.autocomplete = new google.maps.places.Autocomplete(
        this.searchElement.nativeElement,
        {
          componentRestrictions: {
            country: 'de'
          }
        }
      );
      const subscription = this.autocomplete.addListener('place_changed', () => {
        this.ngZone.run(() => {
          const place: google.maps.places.PlaceResult = this.autocomplete.getPlace();
          if (!place || !place.geometry || !place.types || !place.types.length) { return; }
          const placeObject: Place = {
            placeType: place.types[0],
            point: this._convertGeometryToPoint(place.geometry),
          };
          if (place.address_components) {
            this._handleAddressData(placeObject, placeObject.point, place);
          }
        });
      });
    } catch (error) {
      this.logger.error('SearchBarComponent::_loadAutocomplete:Error', error);
    }
  }

  /*
  * Very dangerous logic: if place chosen - returns void
  * If not - returns object
  * If no returns: find first prediction from DOM,
  *   make request by google js file and emit that place
  */

  public async textSearch(address: string): Promise<void> {
    const placeFlag = this.autocomplete.getPlace();
    if (placeFlag == null) return;

    if (this.activatedRoute.snapshot.url[0] && this.activatedRoute.snapshot.url[0].path === 'home')
      return this.goToMainWithData();

    const place = await this.mapsAutocompleteService.textSearch({ query: address });
    const newPlace = this._convertGoogleObjectToPlace(place);
    if (newPlace == null) return;

    this._handleAddressData(newPlace, newPlace.point, place);
  }

  public async onKeypressInput(event: KeyboardEvent): Promise<void> {
    if (!(event.key === 'Enter' || event.code === 'Enter' || event.keyCode === 13 || event.charCode === 13)) return;
    await this.textSearch((event as any).target.value);
  }

  // @FIXME: check should there be any data to be passed and processed before navigation to /map ?
  public goToMainWithData(): void {
    this.router.navigate(['/map']);
  }

  public _convertGeometryToPoint(geometry: google.maps.places.PlaceGeometry): Point {
    return {
      lat: geometry.location.lat(),
      lng: geometry.location.lng(),
    };
  }

  public _collectAddressData(addressComponents: google.maps.GeocoderAddressComponent[]): Address {
    let street;
    let locality;
    let streetNumber;

    for (const addressComponent of addressComponents) {
      if (addressComponent.types.indexOf('locality') > -1) locality = addressComponent.long_name;
      if (addressComponent.types.indexOf('route') > -1) street = addressComponent.long_name;
      if (addressComponent.types.indexOf('street_number') > -1) streetNumber = addressComponent.long_name;
    }

    const address: Address = {
      street,
      locality,
      streetNumber,
    };

    if (this._isAddressValid(address)) return address;
    return null;
  }

  public _convertGoogleObjectToPlace(googleObject: google.maps.places.PlaceResult): Place {
    if (!googleObject || !googleObject.geometry || !googleObject.types.length) return null;
    const placePoint = this._convertGeometryToPoint(googleObject.geometry);
    const placeObject: Place = {
      placeType: googleObject.types[0],
      point: placePoint,
    };
    return placeObject;
  }

  public _isAddressValid(address: Address): boolean {
    return address != null && address.street != null && address.locality != null && address.streetNumber != null;
  }

  public _hideSearch(): void {
    this.mapManager.toggleSearchBar(SEARCHBAR_TOGGLE_TYPES.OFF);
  }

  public _emitNewPlace(place: Place, locality: google.maps.GeocoderAddressComponent): void {
    let zoom: number = 0;

    switch (place.placeType) {
      case 'locality': zoom = 14; break;
      case 'sublocality_level_1': zoom = 15; break;
      case 'sublocality_level_2': zoom = 15; break;
      case 'route': zoom = 18; break;
      default: zoom = 14;
    }

    this.placeChanged.emit({place, zoom});

    if (this.typeOfSearch == null || this.typeOfSearch !== 'withoutGoToMap') {
      this.mapManager.changePlace({place, zoom, name: locality.long_name});
    }
  }

  public _handleAddressData(placeObject: Place, placePoint: Point, place: google.maps.places.PlaceResult): void {
    this.searchHandler(false);
    const address: Address = this._collectAddressData(place.address_components);

    if (this.activatedRoute.snapshot.url[0] && this.activatedRoute.snapshot.url[0].path === 'home') {
      this.goToMainWithData();
    } else {
      if (address == null) {
        this._emitNewPlace(placeObject, place.address_components[0]);
        this._hideSearch();
      } else {
        this._askServerAboutAddressPoint(placePoint, address, placeObject);
      }
    }
  }

  public _askServerAboutAddressPoint(placePoint: Point, address: Address, placeObject: Place): void {
    const property = {
      mapTypeId: 'hybrid',
      latitude: placePoint.lat,
      longitude: placePoint.lng,
      activatedMarkerId: '',
      PropertyType: null,
      zoom: 19,
      showSearchBar: false,
    };
    // @FIXME: temp solution until we dont have API for search items
    this.mapManager.setMapSettings(property);

    this.mapManager.searchProperty({placePoint, address, placeObject});
  }
}
