import { DOCUMENT } from '@angular/common';
import { Injectable, Inject } from '@angular/core';

import { CookieService } from 'ngx-cookie-service';

import { WINDOW } from '../window.provider';
import { environment } from '../../../../environments/environment';
import { GOOGLE_ANALYTICS_KEY } from '../../../app.constants';

import { GTMWindow } from './analytics.model';

const gaDisableCookie = `ga-disable-${GOOGLE_ANALYTICS_KEY}`;

@Injectable()
export class AnalyticsService {
  private get headElement(): HTMLHeadElement {
    return this.document.getElementsByTagName('head')[0];
  }

  public get isAvailableGA(): boolean {
    return !this.cookieService.check(gaDisableCookie) ||
      (this.cookieService.check(gaDisableCookie) && this.cookieService.get(gaDisableCookie) !== 'true');
  }

  constructor(
    @Inject(DOCUMENT) private document: Document,
    @Inject(WINDOW) private window: GTMWindow,
    private cookieService: CookieService,
  ) {
    this.configureDataLayer();
  }

  public activateAnalytics(): void {
    this.activateGoogleAnalytics();
    this.activateGoogleTagManager();
  }

  public triggerFormSubmissionSuccess(formId: string, formContext: string): void {
    const event = 'formSubmissionSuccess';
    this.window.dataLayer.push({
      event,
      formId,
      formContext,
    });
  }

  public disableGoogleAnalytics(): void {
    if (this.isAvailableGA) {
      this.cookieService.set(gaDisableCookie, 'true', new Date('Thu, 31 Dec 2099 23:59:59 UTC'), '/');
    }
    this.window[gaDisableCookie] = true;
  }

  // Google Tag Manager functions best when deployed alongside a data layer.
  // A data layer is a JavaScript object that is used to pass information from your website
  // to your Tag Manager container.
  private configureDataLayer(): void {
    this.window.dataLayer = this.window.dataLayer || [];
  }

  private activateGoogleAnalytics(): void {
    const scriptElement = this.createScriptElement(this.googleAnalyticsScript);
    this.headElement.appendChild(scriptElement);
  }

  private get googleAnalyticsScript(): string {
    return '(function(i,s,o,g,r,a,m){i[\'GoogleAnalyticsObject\']=r;i[r]=i[r]||function(){'
      + '(i[r].q=i[r].q||[]).push(arguments)},i[r].l=1*new Date();a=s.createElement(o),'
      + 'm=s.getElementsByTagName(o)[0];a.async=1;a.src=g;m.parentNode.insertBefore(a,m)'
      + '})(window,document,\'script\',\'https://www.google-analytics.com/analytics.js\',\'ga\');'
      + 'ga(\'create\', \'' + environment.googleAnalyticsKey + '\', \'auto\');'
      + 'ga(\'set\', \'anonymizeIp\', true);';
  }

  private activateGoogleTagManager(): void {
    const scriptElement = this.createScriptElement(this.googleTagManagerScript);
    this.headElement.appendChild(scriptElement);
  }

  private get googleTagManagerScript(): string {
    return '(function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({\'gtm.start\':'
      + 'new Date().getTime(),event:\'gtm.js\'});var f=d.getElementsByTagName(s)[0],'
      + 'j=d.createElement(s),dl=l!=\'dataLayer\'?\'&l=\'+l:\'\';j.async=true;j.src='
      + '\'https://www.googletagmanager.com/gtm.js?id=\'+i+dl;f.parentNode.insertBefore(j,f);'
      + '})(window,document,\'script\',\'dataLayer\',\'' + environment.googleTagManagerKey + '\');';
  }

  private createScriptElement(script: string): HTMLScriptElement {
    const scriptElement = this.document.createElement('script');
    scriptElement.type = 'text/javascript';
    scriptElement.async = true;
    scriptElement.innerHTML = script;

    return scriptElement;
  }
}
