import {
  Component,
  ViewChild,
} from '@angular/core';
import {
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';

import {
  NgxActiveModal,
  NgxModalStack,
} from 'ngx-multi-modal';

import { AuthException, AuthService } from '../../../../core/services/auth.service';
import { PasswordValidators } from '../../../../shared/validators/password.validator';
import { InputComponent } from '../../../../shared/components/input/input.component';
import { markFormGroupControlsAsTouched } from '../../../../shared/utils/form-group.util';
import { isSmallMobileDevice } from '../../../../shared/utils/responsive.util';
import {
  ConfirmationDialogComponent,
  ConfirmationDialogModel,
  ConfirmationDialogType
} from '../../../../shared/components/confirmation-dialog/confirmation-dialog.component';
import { AnalyticsService } from '../../../../core/services/analytics/analytics.service';
import { IUserData } from '../../../../core/models/user.model';
import { EmailValidators } from '../../../../shared/validators/email.validator';
import { SHARED_ROUTES_NAMES } from '../../../../app.constants';
import { LoginComponent } from '../login/login.component';
import { ErrorUnconfirmedEmailComponent } from '../error-unconfirmed-email/error-unconfirmed-email.component';
import {
  ForgotPasswordComponent,
  ForgotPasswordModel
} from '../../../forgot-password/components/forgot-password/forgot-password.component';

export enum SignupResult {
  OpenLogin = 'OpenLogin',
}

@Component({
  selector: 'app-signup-modal',
  templateUrl: './signup-modal.component.html',
  styleUrls: [
    './signup-modal.component.css',
  ],
})
export class SignupModalComponent {
  @ViewChild('emailInput') emailInput: InputComponent;

  public _name: string = 'signup-modal';

  private user: IUserData = {
    email: '',
    password: '',
  };

  public showErrorMessage: boolean = false;

  public termsTriggered: boolean = false;

  public dataProtectionPageLink: string[] = [ `/${ SHARED_ROUTES_NAMES.DATAPROTECTION }` ];
  public termsOfServiceLink: string[] = [ `/${SHARED_ROUTES_NAMES.TERMS_OF_SERVICE}` ];

  public isSmallMobileDevice: boolean = isSmallMobileDevice();

  public verifyForm: FormGroup;

  public acceptTermsConditions: boolean = false;
  // @TODO: use this field when api is ready
  public acceptEmailInform: boolean = false;

  public get email(): FormControl {
    return this.verifyForm.get('email') as FormControl;
  }

  public get password(): FormControl {
    return this.verifyForm.get('password') as FormControl;
  }

  constructor(
    private authService: AuthService,
    private modalService: NgxModalStack,
    private activeModal: NgxActiveModal,
    private analyticsService: AnalyticsService,
  ) {
    this.initForm();
  }

  private initForm(): void {
    this.verifyForm = new FormGroup({
      'email': new FormControl('', [
        Validators.required,
        EmailValidators.pattern,
      ]),
      'password': new FormControl('', [
        PasswordValidators.minLength,
        PasswordValidators.letter,
        PasswordValidators.number,
      ]),
    });
  }

  public toggleAgreementTermsConditions(accept: boolean): void {
    this.acceptTermsConditions = accept;
    this.termsTriggered = false;
  }

  public toggleAgreementEmailInform(accept: boolean): void {
    this.acceptEmailInform = accept;
  }

  public dismiss(): void {
    this.activeModal.dismiss();
  }

  public async signup(): Promise<void> {
    markFormGroupControlsAsTouched(this.verifyForm);

    if (!this.termsTriggered) {
      this.termsTriggered = true;
    }

    if (!this.verifyForm.valid || !this.acceptTermsConditions) {
      return;
    }

    try {
      this.showErrorMessage = false;

      this.user.email = this.verifyForm.value.email;
      this.user.password = this.verifyForm.value.password;

      await this.authService.signUp2(this.user);
      this.analyticsService.triggerFormSubmissionSuccess('signupForm', 'signup');

      this.activeModal.close();

      this.showSignupSuccessModal();
    } catch (error) {
      if (error.message === AuthException.UserNotConfirmed) {
        this.confirmEmail();
      } else if (error.message === AuthException.UserNameExists) {
        await this.resetPassword();
      } else {
        this.showErrorMessage = true;
      }
    }
  }

  public openLogin(): void {
    this.activeModal.close(SignupResult.OpenLogin);
    this.modalService.openFromComponent(LoginComponent);
  }

  public confirmEmail(): void {
    this.modalService.openFromComponent(ErrorUnconfirmedEmailComponent);
  }

  public async resetPassword(): Promise<void> {
    const dialogModel: ConfirmationDialogModel = {
      message: `Deine E-Mail-Adresse existiert bereits bei Scoperty.
      Erstelle Dir ein neues Passwort, um Dich einzuloggen.`,
      type: ConfirmationDialogType.Info,
      buttonLabel: 'Weiter',
    };
    const modalRef = this.modalService.openFromComponent(ConfirmationDialogComponent);
    Object.assign(modalRef.instance, dialogModel);

    modalRef.result.then(() => {
      this.forgotPassword();
    }, () => {
    });
  }

  public forgotPassword(): void {
    const dialogModel: ForgotPasswordModel = {
      email: this.user.email
    };
    const modalRef = this.modalService.openFromComponent(ForgotPasswordComponent);
    Object.assign(modalRef.instance, dialogModel);
  }

  public showSignupSuccessModal(): void {
    const dialogModel: ConfirmationDialogModel = {
      type: ConfirmationDialogType.Custom,
      icon: 'check',
      iconSize: '32px',
      title: 'Herzlichen Glückwunsch! Du bist nun registriert.',
      message: 'Bitte bestätige deine E-Mail, um die Registrierung abzuschließen.',
      linkLabel: 'E-Mail zur Bestätigung erneut senden',
      buttonLabel: 'Verstanden',
    };
    const modalRef = this.modalService.openFromComponent(ConfirmationDialogComponent);
    Object.assign(modalRef.instance, dialogModel);
    modalRef.result.then(result => {
      if (result === 'link') {
        this.sendConfirmUser();
      }
    }, () => {});
  }

  public async sendConfirmUser(): Promise<void> {
    if (this.user.email !== '') {
      await this.authService.resendConfirmation(this.user.email);
    }
  }
}
