import { Injectable } from '@angular/core';

import { CookieService } from 'ngx-cookie-service';

import { AnalyticsService } from './analytics/analytics.service';

const COOKIES_PERMISSION: string = 'cookies-permission';

export type CookiesPermission = 'all' | 'no' | 'ga';

@Injectable()
export class CookiesService {
  private formIsShown: boolean = false;

  constructor(
    private cookieService: CookieService,
    private analyticsService: AnalyticsService,
  ) {
    if (this.cookiesPermission === 'no') this.analyticsService.disableGoogleAnalytics();
  }

  public set cookiesFormIsShown(isShown: boolean) {
    this.formIsShown = isShown;
  }

  public get cookiesFormIsShown(): boolean {
    return this.formIsShown;
  }

  public set cookiesPermission(cookiesPermission: CookiesPermission) {
    this.cookieService.set(COOKIES_PERMISSION, cookiesPermission);
    if (cookiesPermission === 'no') this.analyticsService.disableGoogleAnalytics();
  }

  public get cookiesPermission(): CookiesPermission {
    return <CookiesPermission>this.cookieService.get(COOKIES_PERMISSION);
  }

  public checkPermission(): boolean {
    return this.cookieService.check(COOKIES_PERMISSION);
  }
}
