import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';

import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

import { isSmallMobileDevice } from '../../utils/responsive.util';
import { StateManagerService } from '../../../core/services/state-manager.service';
import { MapFilterService } from '../../../core/services/map-filter.service';


export interface InformationModel {
  title:        string;
  text:         string;
  attention:    string;
  buttonText:   string;
  buttonText2:  string;
  style:        string;
  page:         string;
}

@Component({
  selector: 'app-information-modal',
  templateUrl: './information-modal.component.html',
  styleUrls: ['./information-modal.component.css']
})

export class InformationModalComponent
  extends DialogComponent <InformationModel, string>
  implements InformationModel, OnInit, OnDestroy {

  public title: string = '';
  public text: string = '';
  public attention: string = '';
  public buttonText: string = '';
  public buttonText2: string = '';
  public style: string = '';
  public page: string = '';

  public agreed: boolean = false;

  public isMobileDevice: boolean = document.body.clientWidth < 768;
  public isSmallMobileDevice: boolean = isSmallMobileDevice();

  public _name: string = 'information-modal';

  private _listener(): void {
    this.isMobileDevice = document.body.clientWidth < 768;
  }

  constructor(
    public dialogService: DialogService,
    private mapFilterService: MapFilterService,
    private router: Router,
    public stateManagerService: StateManagerService,
  ) {
    super(dialogService);

    this._listener = this._listener.bind(this);
  }

  public ngOnInit(): void {
    window.addEventListener('resize', this._listener);
  }

  public ngOnDestroy(): void {
    super.ngOnDestroy();
    window.removeEventListener('resize', this._listener);
  }

  public _checkboxChanged(event: boolean): void {
    this.agreed = event;
  }

  public onLogoClick(): void {
    this.router.navigate(['/map']);
    if (this.isSmallMobileDevice) {
      setTimeout(() => {
        this.mapFilterService.toggleFiltersShown(false);
      }, 500);
    }
    this.close();
  }

  public apply(result: string): void {
    if (result) {this.result = result; }
    this._close();
  }

  public _close() {
    if (this.page === 'property' || this.page === 'property-edit') {
      this.stateManagerService.isMapPage = false;
      this.stateManagerService.isStartPage = false;
    }
    this.close();
  }
}
