import {Component, Input} from '@angular/core';

import { ExtendedMarkerModel } from '../../../../core/models/extended-marker.model';
import { templateStatusToFrontend } from '../../../utils/deserializator.util';

@Component({
  selector: 'app-object-slider-item',
  templateUrl: './object-slider-item.component.html',
  styleUrls: ['./object-slider-item.component.css']
})
export class ObjectSliderItemComponent {
  @Input() marker: ExtendedMarkerModel;

  public _name: string = 'object-slider-item';

  private get fullAddress(): string {
    if (this.marker.Url && !this.marker.ShowAddress) {
      return '';
    }
    const {PostalCode, Locality, Street, HouseNumber} = this.marker;
    return `${Street} ${HouseNumber}, ${PostalCode} ${Locality}`;
  }

  public get markerTitle(): string {
    return this.marker.Title ? this.marker.Title : this.fullAddress;
  }

  public get markerAddress(): string {
    return this.marker.Title ? this.fullAddress : '';
  }

  public get hasImage(): boolean {
    return this.marker.images.length > 0;
  }

  public get firstImage(): string {
    return this.marker.images[0];
  }

  public get propertyId(): string {
    return this.marker.PropertyId;
  }
}
