import {
  Component,
  OnInit,
  OnDestroy,
} from '@angular/core';
import {
  trigger,
  style,
  animate,
  state,
  transition
} from '@angular/animations';
import { Router, NavigationEnd, NavigationStart, Event } from '@angular/router';

import { DialogService } from 'ng2-bootstrap-modal';
import { NgxModalStack } from 'ngx-multi-modal';
import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';

import { PRIVATE_URLS, SHARED_ROUTES_NAMES } from '../../../app.constants';
import { RegistrationComponent } from '../../../features/registration/pages/registration/registration.component';
import { LoginComponent } from '../../../features/registration/components/login/login.component';
import { SignupMobileMenuComponent } from '../../../features/registration/components/signup-mobile-menu/signup-mobile-menu.component';
import { RoleChooseComponent } from '../../../shared/components/role-choose/role-choose.component';
import { AuthService } from '../../../core/services/auth.service';
import { UserService } from '../../../core/services/user.service';
import { MapManagerService } from '../../../core/services/map-manager.service';
import { OpenModalService } from '../../../core/services/open-modal.service';
import { StateManagerService } from '../../../core/services/state-manager.service';
import { OverlayService } from '../../../core/services/overlay.service';
import { isSmallMobileDevice, isSmallDevice } from '../../../shared/utils/responsive.util';
import { FeatureToggleService } from '../../services/feature-toggle/feature-toggle.service';

declare let document;

@Component({
  selector: 'app-layout-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
  animations: [
    trigger('fadeOutHeader', [
      transition('* => void', [
        style({height: '307px', opacity: 1, overflow: 'hidden'}),
        animate(500, style({height: '0px', opacity: 0, overflow: 'hidden'}))
      ])

    ]),
    trigger('openDetails', [
      state('true', style({
        width: '480px'
      })),
      state('false',   style({
        width: '0px',
        display: 'none'
      })),
      transition('false => true', animate(600)),
      transition('true => false', animate(600))
    ])
  ]
})

export class LayoutHeaderComponent implements OnDestroy, OnInit {
  private authSubscription: Subscription = null;

  public routerSubscription: Subscription;

  public _name: string = 'navbar';
  public linkToProfessionals: string = SHARED_ROUTES_NAMES.PROFESSIONALS;
  public previousRoute: string = null;
  public isSmallMobileDevice: boolean = isSmallMobileDevice();
  public isSmallDevice: boolean = isSmallDevice();
  public isMobileDevice: boolean = document.body.clientWidth < 768;
  public isTabletDevice: boolean = document.body.clientWidth < 1280;
  public menuProfileOpened: boolean = false;
  public userNotAuthorized: boolean = true;
  public isOpenSignupMenu: boolean = false;
  public searchClicked: boolean = false;
  public profileClicked: boolean = false;
  public isMapIconShown: boolean = false;
  public mapClicked: boolean = false;
  public hamburgerClicked: boolean = false;
  public menuOpened: boolean = false;
  public searchBig: boolean = false;
  public isOpenSignupMenuMobile: boolean = false;
  public activeSubpage: string;
  public isDashboardMenuOpened: boolean = false;

  public get locationPath(): boolean {
    const path = window.location.pathname;

    // @FIXME: Update style for template and delete this getter
    return true;
  }

  public get isMapPage(): boolean {
    return this.stateManagerService.isMapPage;
  }

  public get isProfilePage(): boolean {
    return this.stateManagerService.isProfilePage;
  }

  public get isSearchPage(): boolean {
    const path = (window as any).location.pathname;
    const searchPage = `/${ SHARED_ROUTES_NAMES.MAP }`;
    return path === searchPage;
  }

  public get isSpecialPage(): boolean {
    const path = (window as any).location.pathname;
    const specialPages: string[] = [
      `/${ SHARED_ROUTES_NAMES.MAP }`,
      `/${ SHARED_ROUTES_NAMES.PROPERTY_VALUATION }`,
    ];
    return specialPages.includes(path);
  }

  private checkActiveRoute(event: Event): void {
    if (event instanceof NavigationStart || event instanceof NavigationEnd) {
      if (this.isMobileDevice) {
        this.mapClicked = event.url === '/map' && (event as any).urlAfterRedirects === '/map';
        this.isMapIconShown = !this.mapClicked;
      } else if (
        event.url === `/${SHARED_ROUTES_NAMES.PROFESSIONALS}`
        && (event as any).urlAfterRedirects === `/${SHARED_ROUTES_NAMES.PROFESSIONALS}`
      ) this.activeSubpage = 'Für Makler';
      else if (
        event.url === `/${SHARED_ROUTES_NAMES.SEARCHBROKER}`
        && (event as any).urlAfterRedirects === `/${SHARED_ROUTES_NAMES.SEARCHBROKER}`
      ) this.activeSubpage = 'Maklersuche';
      else if (
        event.url === `/${SHARED_ROUTES_NAMES.BROKER}`
        && (event as any).urlAfterRedirects === `/${SHARED_ROUTES_NAMES.BROKER}`
      ) this.activeSubpage = 'Baufinanzierung';
      else if (
        event.url === `/${SHARED_ROUTES_NAMES.ABOUT}`
        && (event as any).urlAfterRedirects === `/${SHARED_ROUTES_NAMES.ABOUT}`
      ) this.activeSubpage = 'Über uns';
      else if (
        event.url === `/${SHARED_ROUTES_NAMES.HOWITWORKS}`
        && (event as any).urlAfterRedirects === `/${SHARED_ROUTES_NAMES.HOWITWORKS}`
      ) this.activeSubpage = 'Hilfe';
      else this.activeSubpage = '';
    }
  }

  private _listener(): void {
    this.isMobileDevice = document.body.clientWidth < 768;
    this.isTabletDevice = document.body.clientWidth < 1280;
  }

  constructor(
    private dialogService: DialogService,
    private modalService: NgxModalStack,
    private router: Router,
    private authService: AuthService,
    private userService: UserService,
    private mapManager: MapManagerService,
    private openModalService: OpenModalService,
    public stateManagerService: StateManagerService,
    private overlayService: OverlayService,
    public featureToggleService: FeatureToggleService,
  ) {
    this._listener = this._listener.bind(this);

    // @FIXME: there's no navigation* events when map page is loaded\reloaded
    // so tabs at first time are not underlined
    this.router.events.subscribe((event: NavigationStart | NavigationEnd) => {
      this.checkActiveRoute(event);
    });
  }

  public ngOnInit(): void {
    this.userNotAuthorized = !this.authService.isUserLoggedIn();
    this.authSubscription = this.authService.events.subscribe(() => {
      this.userNotAuthorized = !this.authService.isUserLoggedIn();
    });

    this.routerSubscription = this.router.events
      .pipe(
        filter(event => event instanceof NavigationEnd),
      )
      .subscribe((newRoute: NavigationEnd) => {
        const pathName = newRoute.url.slice(1);

        if (this._shouldPopupRun(pathName)) this.showRoleChoose();

        this.previousRoute = pathName;
      });

    window.addEventListener('resize', this._listener);
  }

  public ngOnDestroy(): void {
    this.routerSubscription.unsubscribe();
    this.authSubscription.unsubscribe();
    window.removeEventListener('resize', this._listener);
  }

  public openSubpage(page: string): void {
    if (this.isTabletDevice) {
      this.onHamburgerClickFromNavbar();
    }
    this.activeSubpage = page;
  }

  public toggleProfileMenu(value?: boolean): void {
    if (arguments.length) this.menuProfileOpened = value;
    else this.menuProfileOpened = !this.menuProfileOpened;
  }

  public onLogoClick(): void {
    this.router.navigate(['/']);
    this.mapManager.resetSettingsToDefaultAndNotify();
  }

  public onSearchClick(): void {
    this.profileClicked = false;
    this.mapClicked = false;
    this.searchClicked = !this.searchClicked;
  }

  public onProfileClick(): void {
    this.searchClicked = false;
    this.mapClicked = false;

    if (this.userNotAuthorized) {
      this.openSignupMobileMenuModal();
    } else {
      this.profileClicked = !this.profileClicked;
      this.togglesDashboardMenu(false);
    }
  }

  public onMapClick(): void {
    this.profileClicked = false;
    this.searchClicked = false;
    this.mapClicked = true;
    this.isMapIconShown = false;
  }

  public hideSecondState(): void {
    this.profileClicked = false;
    this.searchClicked = false;
    this.mapClicked = false;
  }

  public onHamburgerClickFromNavbar(): void {
    this.closeHeaderMenu();
    this.hamburgerClicked = !this.hamburgerClicked;
  }

  public onHamburgerClick(): void {
    this.menuOpened = !this.menuOpened;
    this.overlayService.toggleOverlay(this.menuOpened);
  }

  public showSignupFromNavbar(): void {
    this.closeHeaderMenu();
    this.isOpenSignupMenuMobile = true;
  }

  public showLoginFromNavbar(): void {
    this.closeHeaderMenu();
    this.showLogin();
  }

  public logoutFromNavbar(): void {
    this.closeHeaderMenu();
    this.logout();
  }

  public closeHeaderMenu(): void {
    this.profileClicked = false;
    this.searchClicked = false;
  }

  public togglesDashboardMenu(value): void {
    this.isDashboardMenuOpened = value;
  }

  private showLogin(): void {
    const {url} = this.router;
    this._getRedirectUrl(url);
    this.modalService.openFromComponent(LoginComponent);
  }

  public switchDisplaySignupMenu(menuState?: boolean): void {
    if (arguments.length) this.isOpenSignupMenu = menuState;
    else this.isOpenSignupMenu = !this.isOpenSignupMenu;
  }

  public openSignupModal(): void {
    this.router.navigate([{ outlets: { 'modal': [ 'signup'] }}]);
  }

  public openSignupMobileMenuModal(): void {
    this.modalService.openFromComponent(SignupMobileMenuComponent);
  }

  public showRoleChoose(): void {
    this.openModalService.open();
    this.dialogService.addDialog(RoleChooseComponent)
      .subscribe((message) => {
        this.openModalService.close();
      });
  }

  public showRegistration(role: string): void {
    let action = '';
    let category = '';
    switch (role) {
      case 'owner':
        action = 'Eigentümer';
        category = 'reg_global_owner';
        break;
      case 'buyer':
        action = 'Käufer';
        category = 'reg_global_buyer';
        break;
      case 'realtor':
        action = 'Makler';
        category = 'reg_global_realtor';
        break;
    }
    this.isOpenSignupMenuMobile = false;
    this.openModalService.open();
    this.dialogService.addDialog(
      RegistrationComponent,
      { role: role, mode: 'registration' }
    )
      .subscribe((message) => {
        this.openModalService.close();
      });
  }

  public showAuthorization(): void {
    this.isOpenSignupMenuMobile = false;

    this.openModalService.open();

    // to prevent login password-input highlighning with hover effect
    // because the `Bereits registriert?` button of the navbar-menu is on the same position as password-input of login
    // especially in Safari Mobile
    setTimeout(() => {
      this.modalService.openFromComponent(LoginComponent);
      setTimeout(() => {
        this.switchDisplaySignupMenu();
      }, 4);
    }, 50);
  }

  public logout(): void {
    this.authService.logout();
    this.userService.clearUserRole();
    this.userNotAuthorized = true;
    this.authService.send('logout');
    if (PRIVATE_URLS.includes((/^(.*)#|\B/i.exec(this.router.url) || [])[1] || this.router.url)) {
      this.router.navigate(['/map']);
    }
  }

  public _shouldPopupRun(newRouteName: string): boolean {
    return this.authService.signupState &&
      this.previousRoute !== newRouteName &&
      !this._isNewRouteBlacklisted(newRouteName) &&
      (
        this.previousRoute === SHARED_ROUTES_NAMES.PRIVACY ||
        this.previousRoute === SHARED_ROUTES_NAMES.TERMS_OF_SERVICE ||
        this.previousRoute === SHARED_ROUTES_NAMES.ABOUT
      );
  }

  public _isNewRouteBlacklisted(newRouteName: string): boolean {
    const routesBlacklist = [
      SHARED_ROUTES_NAMES.ABOUT,
      SHARED_ROUTES_NAMES.PRIVACY,
      SHARED_ROUTES_NAMES.TERMS_OF_SERVICE,
    ];
    return routesBlacklist.includes(newRouteName);
  }

  public _getRedirectUrl(url: string): string {
    const [rootUrl, ...restUrl] = url.split('/').slice(1);
    if (rootUrl === 'profile') return '/';
    return url;
  }

  public bigSearchHandler(menuState: boolean): void {
    this.searchBig = menuState;
  }
}
